<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                    B. Eugeniusz Bossilkov<br />(1900 - 1952)
                </h1>
                <img src="../assets/images/saints/eugeniusz-bossilkov.jpg" alt="">
                <p>Wincenty Bosikow urodzi si w 1900 roku w Belene, w bugarskiej rodzinie katolickiej. W wieku 14 lat zdecydowa si wstpi do zakonu pasjonistw, prowadzcego swoj dziaalno duszpastersk na terenie Bugarii od 1781 roku. Przebywa nastpnie w Holandii i Belgii, gdzie studiowa m.in. teologi. W 1920 roku zoy luby zakonne przybierajc imi Eugeniusz. Sze lat pniej otrzyma wicenia kapaskie.</p>
                <p>
                    W 1924 roku wrci do Bugarii, jednak niedugo potem zosta skierowany na studia doktoranckie do Rzymu na Papieski Instytut Wschodni. Jego praca doktorska dotyczya unii kocielnej zawartej w XIII wieku przez Bugari ze Stolic Apostolsk. Po powrocie pracowa w urzdach kocielnych, jednak osobicie wola powica czas na spotkania z wiernymi. W zwizku z czym na swoj prob zosta skierowany do pracy w jednej z parafii pooonej we wsi nad Dunajem. Zyska wrd tamtejszej spoecznoci spore uznanie, angaujc si w prac z modzie.
                </p>

                <p>
                    Wraz z przystpieniem Bugarii do II wojny wiatowej po stronie III Rzeszy udziela schronienia i pomocy kilku tysicom przeladowanych ydw. Po wejciu do kraju Armii Czerwonej i przejciu wadzy przez komunistw, rozpocz si w kraju okres bezwzgldnych przeladowa oraz terroru wymierzonego w chrzecijan. Zbiego si to z jego nominacj na ordynariusza nikopolskiego w 1947.
                </p>

                <p>
                    Stosunki pastwo-Koci ulegay coraz wikszemu pogorszeniu. W 1949 roku wszystkie dobra kocielne zostay znacjonalizowane, ograniczono dziaalno zakonw oraz deportowano delegata apostolskiego. Od 1952 wadze zaczy masowo aresztowa duchownych. Taki sam los spotka biskupa nikopolskiego, ktry zosta zatrzymany 16 lipca w Sofii pod zarzutem spisku majcego na celu obalenie wadzy komunistycznej w Bugarii i zdrady pastwa.
                </p>

                <p>
                    Podczas pobytu w wizieniu by poddawany torturom psychicznym, ktrych celem miao by przyznanie si do rzekomej winy. Na pokazowym procesie jako dowd jego przestpstwa pokazano dwa pistolety, bdce czci wystawy znajdujcej si w jednej ze szk katolickich. Zosta uznany za winnego zarzucanego mu przestpstwa, a tre wyroku brzmiaa nastpujco:
                </p>

                <p>
                    Na mocy artykuw 70 i 83 kodeksu karnego, Sd skazuje oskaronego, Ewgenija Bosikowa na mier przez rozstrzelanie, a wszystkie jego przedmioty na skonfiskowanie [...] Doktor Ewgenij Bosikow, biskup katolicki, ukoczy studia religijne we Woszech i by szkolony przez Watykan za dziaalno kontrrewolucyjn i szpiegowsk. Jest jednym z dyrektorw konspiracyjnej organizacji katolickiej. By w kontakcie z dyplomatami z krajw imperialistycznych i przekazywa im informacje o charakterze poufnym. Oskarony zwouje diecezjalne rady, w ktrych zapadaj decyzj majce na celu zwalczania komunizmu poprzez religijne konferencje, ktre odbywaj si w Bugarii pod pozorem prowadzenia dziaalnoci zwanej jako misj. Odwoanie wyroku nie jest moliwe.
                </p>

                <p>
                    Biskup Wincenty Eugeniusz (Ewgenij) zosta rozstrzelany na terenie wizienia w nocy 11 listopada 1952 roku o godzinie 23:30. Jego ciao zostao wrzucone do masowego grobu i nigdy nie zostao odnalezione. Broni si z godnoci, ale nie atakowa nikogo.
                </p>
                <p>
                    Odmwiono mu wszelkich praw. Jego skazanie uznano po upadku komunizmu za mord sdowy.
                </p>
                <p>
                    Trwa tam, gdzie postawia go Opatrzno. I zosta do koca wierny Bogu, Kocioowi, Ojcu witemu i Zakonowi.
                </p>
                <p>
                    Papie Pius XII wspomnia biskupa i jego mczesk mier w swojej encyklice zatytuowanej Orientales Ecclesias, dotyczcej sytuacji Kociow Wschodnich, wydanej w tym samym roku. Data i miejsce mieci Bosikowa nie byy znane a do 1975 roku, kiedy szef bugarskiego MSZ zoy wizyt w Watykanie i zosta bezporednio poproszony przez papiea Pawa VI.
                </p>

                <p>
                    Od pocztku lat 80. XX wieku trway prace dotyczce wyniesienia Eugeniusza Bosikowa na otarze. Na posiedzeniach Kongregacji ds. Kanonizacyjnych w Rzymie zbierano dokumenty na temat ycia i mczeskiej mierci biskupa. Na posiedzeniach z 1993 i 1994 owiadczono, e nie ma adnych przeciwwskaza z punktu widzenia teologicznego i prawnego, aby uzna Bosikowa za mczennika i bogosawionego. 15 marca 1998 roku papie Jan Pawe II dokona w Rzymie beatyfikacji biskupa. Rok pniej zosta zrehabilitowany przez bugarskie wadze pastwowe.
                </p>

            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "EugeniuszBossilkov"
    }
</script>

<style scoped lang="scss">
    @import "../assets/scss/style";

