<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                    Historia Zgromadzenia
                </h1>
                <p>
                    Zgromadzenie Mki Jezusa Chrystusa, Pasjonici, przedtem nazywane Zgromadzeniem Klerykw Bosych
                    Najwitszego Krzya i Mki Pana Naszego Jezusa Chrystusa, zostao zaoone w wieku XVIII przez
                    woskiego mistyka w. Pawla od Krzya, yjcego w latach 1694-1775. Pawe Daneo pochodzcy z licznej
                    rodziny kupieckiej w Ovadzie jako mody czowiek odczu ducha krucjaty i zaangaowania si w wojn
                    przeciwko Turkom, jednake stao si dla niego jasne to, e jego powoaniem nie jest obrona wiary
                    orem.
                </p>
                <p>
                    W dalszych latach dozna wewntrznych natchnie, ktre dotyczyy jego osoby (samotno i ycie w
                    pokucie) i wsplnoty (zaoenie zgromadzenia). W 1720 r. otrzyma wewntrzn wizj znaku Mki
                    Paskiej i stroju zakonnego. Uwiadomi sobie, e ma nosi taki habit aobny na pamitk Mki
                    Naszego Pana oraz oywia "wdziczn pamitk o niej w duszach wiernych". Charyzmatem nowego
                    zgromadzenia miay by wic samotno, ubstwo, pokuta oraz kult Mki Paskiej, rozumiany jako
                    najwikszy wyraz inicjatywy miosnej Boga. Wsparty duchowo przez biskupa F.A. di Gattinara,
                    rozpocz dzieo zakadania nowej wsplnoty, odby 40 - dniowe rekolekcje, notujc swe przeycia
                    mistyczne oraz piszc w tym czasie Regu.
                </p>
                <p>
                    W 1721 r. prbowa otrzyma audiencj u papiea, lecz jego wizyta skoczya si tylko odnowieniem
                    lubu wypenienia charyzmatu i szerzenia pamici i kultu Mki Paskiej. Zdecydowany by te na
                    przyjmowanie innych towarzyszy do swej wsplnoty. Pierwsz samotni przyjt od bp. Gattinara byo
                    Monte Argentario nad Morzem Adriatyckim. W roku 1725 pozna osoby, ktre stay si jego protektorami
                    w Rzymie. Papie Benedykt XIII zachci go do tworzenia grupy ustnie aprobujc nowy instytut. w.
                    Pawe od Krzya pracowa w szpitalu w Rzymie i tam rwnie przyj wicenia kapaskie. Dnia 15
                    maja 1741 r. zostaa zatwierdzona pierwsza Regua nowego instytutu, pod nazw Zgromadzenie Ubogich
                    Klerykw Bosych pod wezwaniem Najwitszego Krzya i Mki Jezusa Chrystusa. W lipcu tego roku
                    pierwszych szeciu towarzyszy zoyo trzy luby zakonne dodajc czwarty lub szerzenia naboestwa
                    i kultu Mki Chrystusa przez medytacj i nauczanie. W 1769 r. papie Klemens XI zatwierdzi
                    zgromadzenie zakonne ze lubami prostymi i przywilejami przysugujcymi zakonom ebrzcym. Pawe
                    Daneo po otrzymaniu duchowego owiecenia na pocztku swej drogi przez prawie 40 lat pozostawa w
                    nocy duchowej, ktra zostaa mu odjta pod koniec ycia.
                </p>
                <p>
                    W chwili mierci Pawa od Krzya instytut liczy sobie 176 zakonnikw i 12 klasztorw. Do poowy XIX
                    wieku instytut dziaa we Woszech. Potem klasztory zostay zaoone w Belgii, Anglii, USA,
                    Hiszpanii , nastpnie w Ameryce Poudniowej, Australii i Francji. Pierwsze zagraniczne fundacje
                    istniay jednak w Walii i Bugarii od 1782 roku pod bezporednim zwierzchnictwem Kongregacji d/s
                    Krzewienia Wiary. W wieku XX Zgromadzenie zostao przeszczepione m.in. do Irlandii, Palestyny,
                    Polski i Niemiec.
                </p>
                <p class="center">
                    <strong>
                        STRONA OFICJALNA ZGROMADZENIA PASJONISTW:
                    </strong>
                </p>
                <p class="center">
                    <span onclick="navigator.app.loadUrl('http://www.passiochristi.org/', {openExternal : true});">www.passiochristi.org</span>
                </p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "History"
    }
</script>

