<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                    26 Bogosawionych Mczennikw z Daimiel<br />
                    ( + 1936)<br />mczennicy hiszpascy
                </h1>
                <img src="../assets/images/saints/meczennicy.jpg" alt="">
                <p>Tym razem nie opisujemy historii powoania jednego Pasjonisty, poniewa bohaterami tego opisu jest grupa Pasjonistw  - mczennikw hiszpaskich.</p>
                    <p>
                        W 1931 r. do wadzy w Hiszpanii doszed republikaski rzd, bdcy pod silnym wpywem rewolucji padziernikowej w Rosji. W wyniku reakcji przeciw tym rzdom wybucha wojna domowa, co pocigno za sob wiele ofiar, take wrd osb duchownych. Wsplnota Pasjonistw z Daimiel staa si take ofiar tych przeladowa. Zakonnicy nie angaowali si po adnej stronie, ale zostali wcignici w wir przeladowa religijnych.
                    </p>
                <p>
                    Pitnastu z nich byo modymi scholastykami (studentami), w wieku 18 do 21 lat, ktrzy przygotowywali si do wyjazdu na odlege misje w Ameryce aciskiej. Towarzyszyli im profesorowie i bracia, ktrzy kierowi ich formacj.
                </p>
                <p>
                    Noc z 21 na 22 lipca zostali wyrzuceni z klasztoru prze 200 uzbrojonych onierzy. Przewodniczy zakonnikom o. Prowincja Nicefor. Zgromadzeni w kociele, odbyli ostatni spowied, a prowincja udzieli im wiatyku.
                </p>
                <p>
                    Nastpnie onierze przepdzili zakonnikw w rodku nocy, podzielili na mae grupki a nastpnie umierceni. W rnym czasie i w rnych miejscach zostali zmasakrowani z powodu jednej "zbrodni": pozostali wiernymi swemu powoaniu zakonnemu. Prowincja i 14 innych zostali zabici 23 lipca, trzech innych 25 lipca, dwch innych 25 wrzenia i szeciu innych 23 padziernika. Liczni spord nich przeczuwali, i wkrtce bd "ukrzyowani" z Chrystusem i perspektywa mczestwa zalewaa ich dusze radoci i pragnieniem dzielenia losu Jezusa. Wielu padao z krzyem w doni, krzyczc: "Niech yje Chrystus Krl". Pytano jednego miertelnie rannego scholastyka, czy bardzo cierpi? "Tak - odrzek - ale jestem przecie Pasjonista!" Umierali za swj nard z umiechem, przebaczajc swoim katom i modlc si o pojednanie narodowe. Dnia 1 padziernika 1989 roku Jan Pawe II dokona beatyfikacji 26 Pasjonistw z klasztoru w Daimiel.
                </p>
                <p>
                    <i>
                        Obywatele Kalwarii, oto przyszo nasze Getsemani! Nasza natura ludzka w sabej swej czci ugina si i lka w perspektywie Kalwarii. Tak jak czowieczestwo Jezusa, jest przeraona i powalona. Lecz Jezus Chrystus jest z nami. Daj wam tego, ktry jest si sabych. Anio umacnia Jezusa. Nas za podtrzymuje sam Jezus. (...)Obywatele Kalwarii, odwagi! Trzeba nam, umrze za Chrystusa.
                    </i>
                    (Sowa o. Nicefora skierowane do wspbraci przed przyjciem wiatyku).
                </p>

            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Martyrs"
    }
</script>

<style scoped lang="scss">
    @import "../assets/scss/style";

</style>


