<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                    B. Pio Alojzy Campidelli<br />
                    (1868 - 1889)
                </h1>
                <img src="../assets/images/saints/pio-campidelli.jpg" alt="">
                <p>Alojzy pochodzi z chopskiej , wiejskiej rodziny z okolic Rimini we Woszech.  Jego rodzice, bardzo religijnie  mieli 6-cioro dzieci, ktre wychowali  wzorowo i pobonie.  Pracowali na gospodarce dzierawionej od innego waciciela, otrzymujc dobre wyniki, uczszczali czsto na naboestwa do kocika we wsi. Alojzy (Luigi) urodzi si jako czwarte dziecko, w domu nazywano go zdrobniale Gigino, od imienia woskiego Luigi. Rodzice mieli wielu krewnych wrd duchownych i zakonnikw,  take z miejscowym kapelanem utrzymywali dobre relacje. Matka Gigino, Filomena codziennie rano spieszya na Msz w. do kocioa, take jej syn od najmodszych lat kocha modlitw  i by szczliwy , gdy moga go zabra ze sob.  Ju w pitym roku otrzyma sakrament bierzmowania, tak, jak pozostae dzieci  w wczesnych czasach.  Luigi od tego momentu zacz si zmienia,  obdarzony zosta darem pobonoci, modlitwy, zosta ministrantem, , pobiera pierwsz nauk szkoln u ksidza w kociele.  W 1874 r na tyfus umar mu ojciec , Jzef Campidelli. Rodzin zaopiekowa si krewny, ktry zacz wymaga take od maego Alojzego pracy fizycznej. Ale kiedy ten si rozchorowa, stwierdzono, e nie nadaje si do ciszych prac. Luigi pomaga wic w gospodarstwie przy lejszych zajciach, ale przede wszystkim chcia si duo uczy, modli. Zosta najlepszym uczniem w klasie.  Pokocha szczeglnie Pana Jezusa Ukrzyowanego, czsto odprawia Drog krzyow, przebywa na Mszy ., w, nawet oszczdza grosze aby zamwi intencj za swego zmarego ojca.</p>
                <p>
                    W 1880 r. pozna Pasjonistw , ktrzy przybyli na stae sanktuarium do Casale, niedaleko rodzinnej wsi Alojzego. Misjonarze gosili misje ludowe, z ktrych korzysta take mody Alojzy. May Campidelli by zachwycony wygldem i postaw Pasjonistw! Marzeniem stao si nosi taki strj i by zakonnikiem.  Zapisa si do wieckiego stowarzyszenia w. Pawa od krzya, otrzyma Szkaplerz Mki Paskiej  z emblematem Pasjonistw.  Mia 12 lat , kiedy w wyniku tak wielkiego pragnienia, uda si z matk do klasztoru w Casale z prob o przyjcie do zakonu. Przeor klasztoru rozpozna w nim  wyjtkowego kandydata, lecz odesa do domu, gdy  musia ukoczy 14 lat, zanim mg by przyjty do alumnatu.
                </p>
                <p>
                    W kocu w roku 1882 , po mszy w. zosta odwieziony przez waciciela majtku i ksidza do klasztoru w Casale, gdzie rozpocz Szko Apostolsk dla chopcw i kandydatw na kapanw. Przyobleczony zosta w habit zakonny przybierajc nowe imi Pio od w. Alojzego. Od samego pocztku wielkodusznie powici si Bogu, czsto ucieka si do Ran Chrystusa,  czsto przyjmowa Eucharysti i przebywa przy tabernakulum, modli si do Matk Bo. Wczytywa si w Pismo w., korzysta  z rad spowiednikw, jednym sowem konsekwentnie dy do witoci.  Ze spisanych przez niego notatek z rachunku sumienia mona wywnioskowa o jego duej determinacji i pragnieniu bycia witym.   W 1887 r. przyj nisze wicenia  i kontynuowa kurs teologii. Liczy wwczas 19 lat. Szykowa si do subdiakonatu i diakonatu, ale Bg zdecydowa inaczej. Zachorowa na grulic puc, na ktr to wwczas nie byo lekarstwa. By to wic wyrany znak od Boga i musi powici swe ycie. I tak te si stao. Umar 3 listopada 1889 r majc 21 lat i 6 miesicy, na kocu wypowiadajc sowa kierowane do Boga i Maryi.  Po jego mierci    do klasztoru zaczy napywa wiadomoci o askach otrzymanych za jego przyczyn. Odbyway si take cae pielgrzymki do sanktuarium w Casale. Kiedy pewnego dnia pielgrzymka z Rimini przybya do znanego o. Pio, kapucyna, ten  zauway : "dlaczego przybywacie do ojca Pio kapucyna, przecie macie u siebie witego Pio pasjonist".
                </p>
                <p>
                    Jan Pawe II po odpowiednim procesie kanonicznym wynis go na otarze 17 listopada 1985 roku.
                </p>
            </div>
        </div>
    </div>
</template>

