<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                    w. Innocenty Canoura<br />(1887 - 1934)
                </h1>
                <img src="../assets/images/saints/innocenty-canoura.jpg" alt="">
                <p>Jest mczennikiem XX wieku, poniewa zgin z rk oprawcw rewolucyjnych w czasie wojny domowej w Hiszpanii. Jego ojczyzn bya Galicja hiszpaska, tereny pnocne pwyspu Iberyjskiego. Urodzi si jako sabe dziecko, mia na imi Manuele. Przez 15 lat przebywa w domu rodzinnym. Jego rodzina cieszya si pobonoci i uczynnoci.  Kiedy zdarzyo si ,e w okolicy misje parafialne prowadzili misjonarze z zakonu Pasjonistw. Wtenczas mody manuele zgosi si do klasztoru w Mondonedo a potem do San Sebastian. Mia braki w wyksztaceniu wic skierowano go do szkoy redniej a po niej w kocu rozpocz nowicjat. W tym czasie otrzyma sakrament bierzmowania i zoy pierwsze luby.  Studiowa nastpnie teologi i otrzyma wicenia kapaskie  w 1913  roku. Ale nie atwo przychodzia mu nauka! Nadrabia wiele zalegoci, mia wad wymowy, ale praca daa efekty. Wyprzedzi nawet innych. Po wiceniach sam sta si wykadowc teologii i filozofii, literatury. Docenia nauk i studiowanie.  By dobrym wykadowc. Jak sam napisa do siostrzeca: nieporwnanie wicej wart jest mody czowiek , ktry zdoby dobre wyksztacenie, ni ten, ktry jest dobrze sytuowany. Pienidze bowiem mona atwo roztrwoni na tysice sposobw, wiedza za trwa tak dugo, jak dugo yje ten, ktry j posiada.  W 1926 r. Po przeniesieniu do Mieres zaj si prac nauczycielsk, misyjn i rekolekcyjn.  Zetkn si wtedy z problemami spoecznymi wczesnej Hiszpanii. Z czasem sytuacja w kraju si zaognia i  doszo do rozruchw i wzajemnej walki pomidzy lewic i prawic. Ofiar tych walk stali si ksia, duchowiestwo jak i ludno cywilna.  Kiedy 4 padziernika 1934 r. O. Innocenty uda si z Mieres do Turon aby spowiada w pierwszy pitek miesica w Kolegium braci Szkolnych, nie przypuszcza, e wkrtce poniesie mier mczesk. Po mszy w. zebrano kapanw i braci, zaprowadzono do siedziby rewolucjonistw. W celi  panowa nastrj modlitwy i wzajemnej yczliwoci. Miejscowa ludno staraa si przekona wadze, e dobrzy nauczyciele potrzebni s w kadym ustroju, ale bezskutecznie.  Okoo pnocy wyprowadzono pasjonist i 8 braci Szkolnych , zawieziono na cmentarz, do przygotowanych dow. Nastpnie rozstrzelano nad krawdzi i zakopano .  Pasjonista Innocenty umiera z przekonaniem e spotka Chrystusa, zjednoczy si z Ukrzyowanym, zapaci mioci do niego, za Mio.</p><p>Mczennikw z Touron  O. Innocentego i Braci Szkolnych beatyfikowa w 1990 r. i kanonizowa w 1999 r. papie Jan Pawe II.</p>
            </div>
        </div>
    </div>
</template>

