<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                    B. Ferdynand Grimoaldo Santamaria<br />
                    (1883 - 1902)
                </h1>
                <img src="../assets/images/saints/grimoaldo-santamaria.jpg" alt="">
                <p>Jego rodzina bya prosta, pobona , mieszkajca we Woszech. Matka i ojciec czsto modlili si na racu,  uczyli modlitwy swoje dzieci, rodzice byi bardzo uczynni, peni dobroci wobec innych ludzi. Ferdynand przyj pierwsz Komuni wit w 1891 r. przygotowujc si do niej bardzo starannie.  Wczeniej zosta do niej dopuszczony, pomimo e dzieci w jego wieku musiay jeszcze dugo czeka na to wydarzenie. Odtd Ferdynand czsto przyjmowa Pana Jezusa, czsto przychodzi na Msz w., zatapia si w modlitwie. Zosta ministrantem i tak czsto suy do rannej Mszy w., oraz na popoudniowych naboestwach, e proboszcz pogratulowa rodzicom takiego  chopca.  Wstpi do Konfraterni Niepokalanej, zrzeszajcej dzieci, a potem do chru parafialnego, bowiem by dobrym piewakiem.  Pasjonici prowadzili w okolicach rekolekcje parafialne, uczyli pieni, ktrych mody Ferdynand szybko si nauczy. Sam po wyjedzie misjonarzy naucza katechizmu i uczy pieni, naladujc w ten sposb zakonnikw. </p>
                <p>
                    Ktrego dnia ojciec jego zaproponowa mu kupno konia i pojazdu, aby mg zosta wonic i zacz uczy si zawodu. Obieca mu szybki zarobek i to e nie bdzie si martwi o przyszo. Modzieniec jednak odpar, ze jego karier jest zosta zakonnikiem. Po tej rozmowie ojciec nie ustpowa, chcia koniecznie , aby jego syn  pracowa i zarabia na ycie. Nastawa wci na niego  a w kocu ten odpowiedzia ojcu: "Widzisz tato jak woda ta pynie i si nie cofa? Tak pyn dni naszego ycia. Pyn do przodu i nie wracaj. Po c przywizywa si do wiata? " W kocu ojciec ustpi. Niedaleko osiedlili si Pasjonici, w miejscowoci Pontecorvo. Ferdynand w tym czasie wstpi do szkoy prowadzonej przez ksidza Antoniego Roscia, ktry by zwizany z zakonem. Chopak stara si dobrze uczy tak, aby szybko ukoczy nauk i wstpi do Pasjonistw. Spraw przyjcia do zakonu zaatwiali mu sami zakonnicy, poniewa znali chopaka i jego sytuacj. Poegna si z rodzicami, jego ojciec odwiz go nawet powozem na stacj kolejow, na pocig do Rzymu. W kocu wysiedli na maej stacyjce w Anagni i udali si do klasztoru Matki Boej  w Pugliano . Tam zastaa ich cisza, skupienie i modlitwa. Ferdynand uczu wyjtkow atmosfer tego klasztoru.  Tym bardziej i rozpocz si wielki Tydzie, czas pokuty i rozpamitywania   Mki Chrystusa. Po krtkim czasie Ferdynand przey rekolekcje a nastpnie obczony w czarny pokutny habit rozpocz nowicjat. Przyj wwczas oficjalnie imi Grimoalda od Oczyszczenia NMP.
                </p>
                <p>
                    Ofiarowa si Przeczystej Dziewicy majc 18 lat. W nowicjacie przeoeni mieli z nim kopot - nie mogli go przyapa na adnym potkniciu,  uchybieniu, bdzie w wypenianiu obowizkw i w przestrzeganiu Reguy.  Nowicjusz Grimoaldo stara si pilnie naladowa modego kleryka, Gabriela, ktry wwczas uznany powszechnie by za witego. Zoy  swoje 4 luby zakonne, przyj emblemat, godo Mki Paskiej, i otrzyma Regu. ycie pniejsze wypenione byo cisym przestrzeganiem Reguy zakonnej, conocnym wstawaniem na modlitwy,  postem, biczowaniem, rezygnacj ze wieckich rozrywek. Takie wwczas obowizyway zasady ycia zakonnego. Wkrtce rozpocz studia filozoficzne w klasztorze w Badia di Ceccano.  Studiowa i modli si za tych, ktrzy wwczas pracowali apostolsko goszc rekolekcje. Studia byy monotonne, mia troch zalegoci w nauce, dlatego musia wiele pracowa i przyjmowa te chtnie pomoc w nauce od innych wspbraci. W zamian za to pomaga albo i zastpowa ich w pracach fizycznych, gdy by dobrze zbudowany i silny. W padzierniku 1902 po Wszystkich witych klerycy udali si na wycieczk, podczas ktrej Grimoaldo poczu si le. W klasztorze lekarz stwierdzi zapalenie opon mzgowych. Mody kleryk utraci przytomno.  Zacz wic chorowa, lee w ku. Chorego odwiedzali wspbracia i budowali si jego cierpliwoci, pokor zdaniem si na wol Bo. Mimo wielu stara, jego ycie zbliao si ku kocowi. Jeszcze musia w czasie swej choroby przey walk z siami szatana, poniewa znaleziono go kracowo wykoczonego przy zdemolowanym i przewrconym ku.  W rod 19 listopada wyspowiada si, przyj Komuni wit,  i powiedzia , i odejdzie  przed zachodem soca. Czu si jednak szczliwy, spokojny. Rzeczywicie umar wieczorem, z rumiecem na twarzy. Wie o jego witoci rozesza si wrd mieszkacw Italii a take wrd emigrantw w USA.  Tam te wielu jego rodakw otrzymywao aski za jego wstawiennictwem.  Jan Pawe II dokona beatyfikacji kleryka Grimoaldo Santamaria  dnia  29 stycznia 1995 roku.
                </p>
            </div>
        </div>
    </div>
</template>

