var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"block"},[_c('div',{staticClass:"page"},[_c('div',{staticClass:"page__blur"}),_vm._v(" "),_c('div',{staticClass:"page__content"},[_c('h1',[_vm._v("\n                Pasjonici w Polsce\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n               Zaoyciel od pocztku marzy o tym, aby przeszczepi zakon na tereny polskie, do czego jednak za jego ycia nie doszo z powodw politycznych. Pierwszy rozbir Rzeczpospolitej oraz nieprzyjazne gesty zaborcw wobec Kocioa Katolickiego nie sprzyjay tej idei. Par lat po mierci Pawa Daneo, prby tej chcia dokona w 1781 r. wielki referendarz litewski Wincenty hr. Tyszkiewicz, pragn ofiarowa Pasjonistom klasztor w wisoczy, w diecezji wileskiej. Przy aprobacie miejscowego ordynariusza oraz Sejmu, zakon jednak nie otrzyma zgody samego krla Polski, co byo warunkiem sine qua non ze strony wczesnego generaa w Rzymie na przybycie zakonu do Polski. Kolejne nieudane prby podejmowane byy w latach 1913/1914 w Galicji. Gwny udzia w tym dziele mia o. Jan Konstanty Giecewicz, obywatel cesarstwa Austro-Wgier, w zakonie Zmartwychwstacw penicy funkcje konsultora lub prokuratora, potem przeniesiony do Pasjonistw lecz z niepowodzeniem. Ostatecznie pozosta w swym dawnym Zgromadzeniu. Jego osoba bya przedmiotem zainteresowania papiea Piusa X, ktry poleca Generaowi Pasjonistw przyjcie go do zgromadzenia z myl przeszczepienia zakonu na tereny polskie. Ksidz Giecewicz pragn , by pierwszy klasztor powsta na terenie zaboru austriackiego, a konkretnie na Morawach, w diecezji Brno, w zamku Frin , nalecym do Adama Stadnickiego, krewnego bp A. Sapiehy. Listy Giecewicza pisane do Generaa Pasjonistw wiadcz o pragnieniu zaproszenia instytutu do siebie. Poparty by w tej idei rwnie gosem biskupa diecezji Brno, Pawa Hunga oraz innych biskupw , take polskich. Jednake, mimo zacht i pragnie hr. Stadnicki postawi pewne warunki pobytu zakonu na zamku. Brak staego zabezpieczenia warunkw mieszkaniowych i konieczno podjcia duszpasterstwa parafialnego przyczyniy si do odmowy ze strony wadz zakonnych w Rzymie. Dalsze starania ks. Gieczewicza skoncentroway si na prbach zaoenia klasztoru w Polsce tak, aby mona byo unikn fundacji o charakterze niemieckim i unikn antagonizmw narodowych. W tym czasie ks. abp. A. Sapieha poinformowa go o kilku ochotnikach z zaboru rosyjskiego, ktrzy chcieliby wstpi do Pasjonistw. Starania J.K.Giecewicza w kocu Skippy si na fundacji, ktra byaby pooona na pograniczu wszystkich zaborw z moliwoci rozszerzania jego dziaalnoci przez wzbudzanie powoa na wszystkich ziemiach polskich. Peen zapau i entuzjazmu spotka si jednak z trudnociami politycznymi a take z pasywn postaw wczesnego Generaa, tym bardziej, i by to koniec kadencji wadz w Rzymie, ktre nie chciay wiza Zgromadzenia pochopnymi zobowizaniami przed kolejn1 Kapitu Generaln. Ks. Giecewicz przyznaje w licie do przeoonych Zgromadzenia, e win za fiasko przeszczepienia Pasjonistw na ziemie polskie bierze na siebie. W 1914 r. wprawdzie zachcany jeszcze przez nowego Generaa, nie czuje si na siach do dalszych dziaa w tym dziele.\n           ")]),_vm._v(" "),_c('p',[_vm._v("\n                Ju przed pierwsz wojn wiatow do Zgromadzenia Pasjonistw wstpowali pierwsi Polacy tacy jak: o. A.Urbanowicz, o.L.Dowal, o.G. Piega, o.J.Dzidowski, br.K.Staszewski, br.W. Kamieski. Niektrzy z nich przybyli do Polski aby przeszczepi instytut do ojczyzny po odzyskaniu przez ni niepodlegoci. Wraz z wyborem kard. A. Rattiego na papiea, ktry wczeniej by nuncjuszem na terenie Polski, pojawia si wiksza szansa na obecno Zgromadzenia w Polsce. Po rozmowach o. Generaa z papieem, ktry \"wyrazi gorce pragnienie\" przybycia Zgromadzenia do Polski oraz po spotkaniu z polskimi biskupami, doszo do dugiego procesu rozmw i propozycji pomidzy Zgromadzeniem, poszczeglnymi biskupami w Polsce oraz samym papieem. Prym w rozmowach wiedli arcybiskup Julian Nowowiejski z Pocka, pomocniczy bp Stanisaw Gall z Warszawy , bp oziski z Kielc oraz bp Zygmunt oziski z Piska. Ojciec Genera po odnalezieniu Pasjonistw polskiego pochodzenia, zdecydowa si wysa na rekonesans dwch zakonnikw , o. Juliusza od Najw. Serca Maryi (Jan Dzidowski) oraz brata Kazimierza od Matki Boej Bolesnej (Kajetan Staszewski), Emisariusze po audiencji u Piusa XI i u kardynaa Gaspariiego, sekretarza stanu, od ktrego otrzymali list polecajcy dla Nuncjusza Apostolskiego w Warszawie, Wawrzyca Lauri , przybyli do Polski w kwietniu 1923 roku.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Zakonnicy podrowali po terenach polskich i odwiedzali proponowane miejsca. Oferowano wic do wyboru klasztor i koci poaugustiaski w Rawie Mazowieckiej, pobernardyski w owiczu, pobernardyski klasztor w Przasnyszu lub w Ostroce, take w Ksiu Wielkim, pofranciszkaski w Chcinach, na Grodzisku pod Ojcowem w parafii Skaa, pobernardyski klasztor na Karczwce pod Kielcami, pobazyliaski w Pisku, a take opactwo pocysterskie w Jdrzejowie i pobernardyski klasztor w Piczowie. Szczeglne starania w tym wzgldzie podejmowa abp pocki, ktry nawiza w tym celu bezporedni kontakt z Ojcem witym i o. Generaem. Ostatecznie wic Pasjonici zdecydowali si na miejscowo Przasnysz w diecezji pockiej. Klasztor bernardyski, opuszczony po upadku Powstania Styczniowego i czciowo zniszczony po dziaaniach wojennych, zosta przekazany \"na wieczne uywanie\" pismem z dnia 13 czerwca 1923 roku, wydanym przez abpa Nowowiejskiego. Dnia 17 czerwca nastpio przejcie klasztoru przez Pasjonistw w obecnoci konsultora generalnego przybyego z Woch. Nieobecny na tych uroczystociach abp Nowowiejski zwrci si do Pasjonistw, kapanw i ludnoci Przasnysza w licie, w ktrym zachca midzy innymi do wsparcia zakonnikw, korzystania z ich posugi, oraz udziela bogosawiestwa: \"Ronijcie w liczb, zdobywajc sobie jak najwicej powoa, pracujcie, a owoc prac waszych niech bdzie setny\".\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Ostatecznie w roku 1923 o. Juliusz Dzidowski oraz brat Kazimierz Staszewski wraz z delegatem Generaa o. Tyberiuszem Menica dokonali przejcia pobernardyskiego klasztoru w Przasnyszu. Zadecydowao o tym wyborze przede wszystkim pooenie klasztoru, z dala od centrum miasta, w odosobnieniu. Przeoonym klasztoru zosta Woch o. Bartomiej Rapetti. Do pierwszych zakonnikw pod koniec tego roku doczali inni zakonnicy rnych narodowoci. Kilkanacie lat obecnoci Pasjonistw w Polsce to okres dominacji Wochw, ktrych zadaniem byo zakorzenienie Zgromadzenia Mki Paskiej na ziemiach polskich. Pod koniec roku 1930 byo ju w Polsce 11 braci oraz nowe powoania, a jedyny klasztor w Przasnyszu, gdzie by nowicjat i alumnat, stawa si za may na tak szybko rozwijajce si Zgromadzenie. W Przasnyszu miecia si te tzw. Szkoa Apostolska, penica rol Niszego Seminarium, w ktrej to szkole modzi chopcy zdobywajc wyksztacenie rednie mogli by rwnie dobrym materiaem na przyszych zakonnikw. Zaczto rozglda si za innym miejscem pobytu zakonnikw, a uatwi te spraw prymas kard. A. Hlond, ktry listownie obieca pomoc. Bdc w 1932 r. w Poznaniu spotka hr. B. Szembeka z Wysocka Wielkiego koo Ostrowa Wielkopolskiego, ktry zwierzy si mu z planw zaproszenia jakiego zakonu na swoje posiadoci i postawienia klasztoru we wsi Sadowie. We wspomnieniach spisanych przez hr. B. Szembeka , nadesanych w 1980 r. przez jego crk, pisze, i pomys ten mia by wyrazem wdzicznoci z jego strony za ocalenie i przetrwanie wojny wiatowej. Powiadomieni o chci zaproszenia zgromadzenia na Wielkopolsk, o. Bartomiej Rapetti i o. Pius Falco udali si na rozmow z fundatorem miejsca do Wysocka, a nastpnie do kardynaa A. Hlonda w Poznaniu. Otrzymujc 10 ha terenu, zagwarantowali sobie samotno i odosobnienie. Po uzyskaniu zgody prymasa na otwarcie klasztoru w Sadowiu na terenie diecezji poznaskiej oraz zgody o. Generaa, rozpoczto budow pitrowego budynku i nieco mniejszej kaplicy. Po ukoczeniu podstawowych prac dnia 6. listopada 1932 roku przy udziale prymasa Hlonda, dokonano powicenia obiektw. Ojciec Bartomiej Rapetti witajc zebranych goci wyrazi nadziej, \"e kardyna bdzie mg znowu przyby, by pobogosawi w Sadowiu nowy koci i klasztor. Wtedy ta kaplica bdzie suya dla rekolektantw, gdy zamiarem Zgromadzenia jest, aeby klasztor sadowski suy zakonowi jako dom rekolekcyjny, a zarazem jako dom nowicjatu Zgromadzenia\". Patronem klasztoru zosta w. Jzef, patron ycia wewntrznego. Powsta wic w Polsce drugi klasztor Pasjonistw, ktry przed wojn suy za miejsce nowicjatu a do roku 1969. W latach 1934 - 1935 dobudowano budynki gospodarcze, stodo, zalesiono pagrek i posadzono sad oraz postawiono trzy krzye, na ktre materia ofiarowa m.in. hr. B.Szembek. Prekursorami sadowskiego klasztoru byli ojcowie: Bartomiej, Pius i Juliusz Dzidowski, oraz brat Franciszek Uarowski.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Trudne warunki na pocztku pobytu Pasjonistw w Polsce byy przyczyn wysyania klerykw na studia i formacj do Woch. Drug i ostatni fundacj przyjt przez Pasjonistw przed II wojn wiatow by poaugustiaski klasztor w Rawie Mazowieckiej. Instytut otrzyma stare zabudowania oraz koci od kard. A. Kakowskiego, dekretem z dnia 23.10.1937 r. W nastpnym roku odby si ingres oraz porzdkowanie budynkw, odnawianie zdewastowanego kocioa i klasztoru. Pasjonici przybyli tam ostatecznie w 1939 roku, kiedy przeoonym by o Juliusz Dzidowski. Przed sam jednak wojn doszo do nieporozumienia pomidzy instytutem a miejscow Kuri w Warszawie, ktra chciaa narzuci wbrew Reguom zakonnym i decyzjom wadz zakonnych w Rzymie, stanowisko prefekta w szkole podstawowej w Rawie. W wyniku tego sporu jeden zakonnik musia przenie si do Sadowia, a klasztor pozosta tylko rezydencj z kustoszem na czele. Fakt utrzymania si placwki by jednak zbawienny dla pniejszych wydarze z czasw wojny.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                II wojna wiatowa pocigna za sob tragiczne skutki rwnie wrd Pasjonistw w Polsce. Zgromadzenie liczce w tym kraju na pocztku wojny 50 zakonnikw: 18 kapanw, 14 braci, 18 klerykw, rozmieszczonych w 3 klasztorach, zostao poddane prawdziwej prbie, sprawdzajcej autentyczno powoania.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Klasztor w Rawie Mazowieckiej pozosta jako jedyny wasnoci Zgromadzenia, gdy zarwno teren Przasnysza jak i Sadowia przyczono do Rzeszy Niemieckiej. W wyniku ucieczki przed frontem albo likwidacji klasztorw polscy Pasjonici zmuszeni byli opuci te placwki. Take groba aresztowa pojawia si w Rawie, dlatego cz zakonnikw zmuszona bya do ucieczki na wschd , w kierunku Piska, cz za ukrya si w prywatnych domach, a klerykw rozesano do domw rodzinnych. Po jakim czasie klasztor ten zamieni si w wojenny szpital. Zakonnicy , ktrzy powrcili w 1940 roku (B. Kryszkiewicz, o. Stefan Szafraniec, o. Tytus Koakowski, o. Leon Morawski, trzej klerycy), rozpoczli dziaalno duszpastersk, doczajc take do niej opiek nad rannymi onierzami. Placwka w Rawie bya pod sta obserwacj ze strony wadz okupacyjnych. Klasztor suy take za schronienie dla przymusowych przesiedlecw z Wielkopolski, z pnocnego Mazowsza, z Polesia i w kocu dla mieszkacw Warszawy po upadku Powstania Warszawskiego w 1944 roku. Wszelkie pomieszczenia, cznie z zakonn kaplic skrztnie wykorzystano do ulokowania tych wszystkich osb, ktre szukay schronienia. Udzielano wszelkiej pomocy: duszpasterskiej, sanitarnej, lokalowej i ywieniowej z udziaem take osb wieckich. Klasztor w Przasnyszu, ktry znalaz si w obrbie Rzeszy Niemieckiej, na pocztku wojny by oszczdzony w tragicznych losach wojennych. Suy za szpital wojskowy, a zakonnicy mieszkali pocztkowo w pralni, stolarni i subwce, ostatecznie musieli przenie si do sistr Kapucynek. Klasztor zosta nastpnie przeznaczony na spichlerz zboowy, dalej dewastowany i opuszczony. Wyposaenie sakralne zostao usunite i wykupione przez miejscowego proboszcza kocioa farnego.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Dnia 2.04.1941 roku aresztowano ca tamtejsz wsplnot wraz z o. przeoonym o. Sylwiuszem Pajewskim i wywieziono razem z tamtejszymi siostrami Kapucynkami Klaryskami do obozu koncentracyjnego w Dziadowie (Soldau). Tragiczne momenty aresztowania Pasjonistw i Sistr Kapucynek s opisane w Kronice klasztornej tyche sistr w Przasnyszu. Jak relacjonuj siostry Kapucynki, w obozie przetrzymywano Pasjonistw w bardzo cikich warunkach, bito ich, kopano i maltretowano. Na pocztku sierpnia wszyscy ksia i duchowni, ktrzy mogli porusza si o wasnych siach musieli stawi si w komendzie obozu celem sporzdzenia listy i uzupenienia danych personalnych. Nastpnie rozpoczto wywoenie kapanw w nieznanym kierunku, za liczne powiadomienia rodzin o zgonie w tym okresie wiadcz o masowych egzekucjach. W obozie albo w tzw. lasku biauckim zamordowano take Pasjonistw, zbrodnia dokonana zostaa prawdopodobnie 8 sierpnia 1941 roku. Mczesk mierci zgino cznie siedmiu zakonnikw: o. Czesaw Zaga o. Jzef Rosiski o.Laurencjusz Bugaj o.Sylwiusz Pajewski, przeoony, br. Tadeusz Siwoski, br. Leonard Kuskowski i br. Antoni Glinka, ktrzy zmarli prawdopodobnie wczeniej w obozie. Ojciec Czesaw Zaoga zdy napisa jeszcze list do brata Franciszka Uarowskiego o swym pobycie w obozie. By przekonany o wypenianiu si woli Boej i przewiadczony o sodyczy tego strasznego cierpienia dla Chrystusa.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Rwnie tragiczny los spotka klasztor zbudowany przez Pasjonistw w Sadowiu, gdzie przebywao kilku zakonnikw do lipca 1941 roku, po powrocie z tuaczki. Wwczas to klasztor zosta przejty przez Niemcw i przeznaczony na szkolenie modych nazistw , tzw. Hitlerjugend. Usunito trzy stojce krzye, wzniesiono maszt ze swastyk. Koci sadowski zosta zamieniony na sal taneczn i gimnastyczn , w miejsce otarza ustawiono kominek ze swastyk , rozebrano rwnie dzwonnic. Zakonnicy zdyli przy pomocy miejscowej ludnoci ukry sprzt kocielny oraz dzwony. Cz zakonnikw udaa si do domw rodzinnych, za niektrzy ukrywali si w pobliskich okolicach udzielajc si duszpastersko, w Wysocku, Sadowiu i Ostrowie Wielkopolskim. Jeden z ukrywajcych si zakonnikw, br. Wacaw Kamieski , aresztowany przez gestapo u miejscowego gospodarza, zmar w obozie w Dachau prawdopodobnie w 1943 roku.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                W cigu kilkunastu lat przed wojn na terenach polskich przebywao wielu Pasjonistw z innych krajw, wielu z nich byo pochodzenia polskiego. II wojna wiatowa przerwaa t misje i zakonnicy przebywajcy w wiceprowincji polskiej zdani byli na siebie. Niektrzy w czasie wojny przebywali w Rzymie i ten czas przeznaczyli na studia specjalistyczne, co przydao si przy odtwarzaniu i wzmacnianiu Zgromadzenia po powrocie do Polski. W roku 1946 nowicjat w Polsce liczy zaledwie kilku nowicjuszy, potem stopniowo liczba si powikszaa osigajc w roku 1950 trzynastu, co wzmocnio liczebnie polsk wsplnot, Pasjonistw. Przez lata powojenne w Przasnyszu mieci si rwnie dom studiw, ktrego rektorem by biblista, o. Micha Stolarczyk.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                W pierwszych latach pobytu Pasjonistw w Polsce utworzono jednostk administracyjn zwan komisariatem, na ktrego czele stan o. Bartomiej Rapetti, ktry peni funkcj komisarza generalnego a do momentu opuszczenia Polski w 1936 r. Wtedy jego nastpc zosta o. Stanisaw Michalczyk. Nastpnie utworzona zostaa wiceprowincja za wiceprowincjaem zosta o. Juliusz Dzidowski. o. Stanisaw Michalczyk w czasie wojny by przeoonym w Rawie i waciwie take peni obowizki wiceprowincjaa, gdy o. Juliusz Dzidowski przebywa w tym czasie poza klasztorem. Po drugiej wojnie wiatowej w 1958 r. Stolica Apostolska erygowaa polsk prowincj zakonna Pasjonistw pod wezwaniem Wniebowzicia NMP (ASSUM). Pierwszym prowincjaem zosta mianowany w Rzymie podczas Kapituy Generalnej wczesny wiceprowincja o. Jan Wszdyrwny, wychowanek o. Bernarda Kryszkiewicza z lat wojennych, obecnego Sugi Boego, ktrego proces beatyfikacyjny jest w toku. W latach powojennych objto te inne placwki, w Warszawie, Wile, odzi, Olsztynie oraz na Ukrainie w Smotryczu oraz w Czechach na Morawach, Warszawie-Biaoce. Polacy te pracowali w krajach takich jak Zair, Anglia, Szwecja, RPA. W ostatnich latach liczba Polakw z Zgromadzeniu Mki Paskiej zmniejszya si i wynosi ok. 60 czonkw.\n            ")])])])])}]
