<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                    w. Gemma Galgani<br />
                    (1878 - 1903)
                </h1>
                <img src="../assets/images/saints/gemma-galgani.jpg" alt="">
                <p>Maria Gemma Umberta Galgani urodzia si 12 marca 1878 w Borgonuovo di Camigliano. Wielka wita katolicka, dziewica, woska mistyczka, stygmatyczka.</p>
                <p>
                    Pochodzia z wielodzietnej rodziny aptekarza. Po mierci matki (1886) opiekowaa si dziemi znajomego aptekarza (osierocona zostaa z siedmiorgiem rodzestwa w 1897 roku). 8 grudnia 1899 w czasie uroczystoci Niepokalanego Poczcia, zoya lub dziewictwa.
                </p>
    <p>
        Choroby uniemoliwiy jej wstpienie do zgromadzenia mniszek pasjonistek klauzurowych. Na jej ciele pojawiy si rany, cierpiaa take na chorob Potta. Miaa prowadzi rozmowy z Jezusem i Matk Bo dotyczce cierpienia niezawinionego. Zmara w wigili Wielkanocy, 11 kwietnia 1903 w Lukce.
    </p>
                <p>
                    Beatyfikowana 14 maja 1933, przez papiea Piusa XI, Pius XII kanonizowa w. w Bazylice w. Piotra na Watykanie 2 maja 1940. Bya pierwsz wit zmar i kanonizowan w XX wieku. Powodem uznania jej witoci stao si wiadome, milczce przyjcie cierpienia przez w. Gemm.
                </p>
                    <p>
                        Atrybutem witej jest lilia, a jej wspomnienie liturgiczne obchodzone jest w archidiecezji lukkijskiej i w zgromadzeniach pasjonistowskich 16 maja.
                    </p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "GemmaGalgani"
    }
</script>

<style scoped lang="scss">
    @import "../assets/scss/style";

</style>


