e ma sztucznych podziaw czy klas. Pod krzyem sprawy prestiowe malej. Rozrnienia wynikajce w sposb naturalny z funkcji, rekompensuje atmosfera rodzinna, duch braterski, ktry panuje przy wsplnym stole, we wsppracy, w podejmowaniu wsplnych decyzji, w dzieleniu si wzajemnie trudnociami, a take radoci.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Pomimo ewolucji, jak przechodzio zgromadzenie przez dwiecie osiemdziesit lat swej historii, nie zatracio ono swoich ideaw wytyczonych przez Zaoyciela. W Konstytucjach czytamy: \"wiadomi, e Mka Chrystusa trwa na tym wiecie a do powtrnego przyjcia Chrystusa w chwale, dzielimy trudy i radoci ludzkoci w jej drodze do Ojca. Pragniemy dzieli nieszczcia wszystkich, a zwaszcza ubogich i opuszczonych, podnoszc ich na duchu, pomagajc nie ciar ich utrapie. Przez moc Krzya, bdc mdroci Bo, dymy usilnie do poznania i usunicia przyczyn za, ktre drczy ludzi. Dlatego te nasze posannictwo opiera si na ewangelizacji poprzez posug Sowa Krzya tak, aby wszyscy mogli pozna Chrystusa i moc Jego Zmartwychwstania. Kady czowiek, uczestniczc w cierpieniach Chrystusa, moe sta si podobnym do Niego w mierci, aby mie udzia w jego chwale. Wszyscy uczestniczymy w tym apostolstwie. Kady wedug swoich zdolnoci, talentw i moliwoci\" (Konstytucje Zgromadzenia Pasjonistw, 3).\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Uczestnictwo pasjonisty w Mce Chrystusa otrzymuje w profesji zakonnej wymiar prawny, okrelony mianem \"pasyjnej konsekracji\". \"Nasz udzia osobisty, wsplnotowy i apostolski w Mce Chrystusa wyraamy specjalnym lubem. Przez ten lub zobowizujemy si szerzy pamitk Mki Chrystusa sowem i czynem, a take pogbia wiadomo o jej znaczeniu i dla kadego czowieka i dla ycia wiata\" (Konstytucje Zgromadzenia Pasjonistw, 6). Ta szczeglna dedykacja, wyraajca si w pasyjnej interpretacji trzech lubw zakonnych: ubstwa, czystoci i posuszestwa. Wszyscy zakonnicy usiuj realizowa je cznie, w trudzie dnia codziennego.\n            ")]),_vm._