<template>
    <div class="block">
    <div class="page">
        <div class="page__blur"></div>
        <div class="page__content">
            <h1>
                w. Pawe od Krzya
            </h1>
            <img src="../assets/images/pawel.jpg" alt="">
            <p>
                Pawe od Krzya, wac. w. Paolo Francesco Danei  urodzi si 3 stycznia 1694 w Ovada we woskim Piemoncie. Pocztkowo walczy w armii weneckiej przeciwko Turkom. Rozmiowany w Krzyu Chrystusa, nauczy si mdroci w Ranach Chrystusowych. By wielkim mistykiem chrzecijaskim, a zarazem jednym z najwikszych kaznodziejw osiemnastego stulecia. Naucza, e "bardzo poyteczn i wit rzecz jest rozmyla o Mce Pana i nad ni si zastanawia, albowiem na tej drodze dochodzi si do nadprzyrodzonej cznoci z Bogiem.

            </p>
       <p>
           W tej witej szkole nabywa si prawdziwej mdroci. Tam wyuczyli si jej wszyscy wici..."
       </p>
            <p>
                yciorysy w. Pawa od Krzya uznaj zgodnie, e inicjatywa zaoenia Zgromadzenia Mki Paskiej wysza nie od niego, lecz od Matki Boej.
            </p>
<p>Gdy mia 26 lat, ukazaa mu si odziana w czarn szat ze znakiem Mki Jezusa na piersi, znakiem krzya i serca z napisem: JESU XPI PASSIO (Jezusa Chrystusa Mka). Najwitsza Matka polecia Pawowi, by zaoy zakon, ktrego czonkowie odziani w podobny strj bd nieustannie czci pami Mki Jezusowej.</p>


            <p>
                W kolejnych wizjach otrzymywa wiato i pouczenie dotyczce reguy zakonnej i apostolstwa.
                Obdarzony mistycznymi askami, pouczony drog objawie, zaoy zgromadzenie zakonne, ktrego zadaniem jest gosi Chrystusa Ukrzyowanego
                Czonkowie zakonu specjalnym lubem zobowizuj si krzewi w wiecie pami o Mce Chrystusa i naboestwo do niej.

            </p>
            <p>
                Pawe twierdzi, e Mka Jezusa Chrystusa to najwiksze dzieo Boej mioci. Po obczynach przyj imi Pawe od Krzya. W 1737 roku w. Pawe zaoy pierwszy zakonny dom pasjonistw Ritiro, tzn. odosobnienie. Regu zakonn nowego zgromadzenia zatwierdzi 15 maja 1741 roku Benedykt XIV.
            </p>
            <p>
                Koci nie tylko uroczycie zatwierdzi zgromadzenie Mki Paskiej, ale obdarzy je licznymi askami i przywilejami.
                Nazwa w. Pawa od Krzya "owc dusz"
                I tym owc dusz pozosta on do koca. W czasie ostatniej choroby nie przestawa gosi chway Chrystusa Ukrzyowanego.
                Nie mogc zdoby si ju na inne sowa, poprzestawa na zalecaniu odwiedzajcym go, by pamitali o Mce Paskiej.
                Kademu, jako ostatni pamitk, wrcza may krucyfiks, ktry nieustannie mia przypomina ostatnie jego sowa.
                A gdy ju nie mg mwi, uj w drce wyniszczone donie jeden z maych krucyfiksw, wskaza na posta rozpit na krzyu, nastpnie unisszy rk przyoy j do czoa, a arliwe baganie w jego oczach mwio wyraniej ni sowa, na ktre nie potrafi si ju zdoby: "Trzeba zawsze pamita o cierpieniach naszego Pana!" By to ostatni wysiek owcy dusz. Zmar 18 padziernika 1775  w Rzymie w  wieku lat 81.
            </p>
            <img src="../assets/images/grob.jpg">
            <p>
                Ciao witego Zaoyciela doznaje wielkiej czci w rzymskiej bazylice witych Jana i Pawa w Rzymie na wzgrzu Celio.
            </p>
            <p>
                Jego beatyfikacji (1 padziernika 1853) i kanonizacji (29 czerwca 1867) dokona papie Pius IX. Relikwie spoczywaj w kociele witych braci mczennikw Jana i Pawa w Rzymie (SS. Giovanni e Paolo in Roma).
            </p>
            <p>
                w. Jan Pawe II, jeszcze jako kardyna Wojtya, opublikowa artyku o w. Pawle od Krzya w "Ateneum Kapaskim" (t. 70/ 1967) z okazji setnej rocznicy kanonizacji pt. "Duchowo pasyjna i duchowo paschalna w. Pawa od Krzya".
                Dostpny w jzyku polskim yciorys w. Pawa od Krzya nosi tytu: owca dusz".

            </p>


            <h2 class="small">
                Chronologia
            </h2>

            <ul>
                <li>Urodzony w Ovada w pnocnych Woszech, wychowany w rodzinie kupieckiej  3 01.1694</li>
                <li>Czas cakowitego nawrcenia  1713 -1714</li>
                <li>Zacignicie si do wojska na wojn z Turkami  1716</li>
                <li>Przyjcie sakramentu bierzmowania w kociele Matki Boej  23.04.1719</li>
                <li>Wielka wizja o swym powoaniu w Castellazzo  1720</li>
                <li>Przyjcie tuniki Pasjonisty od biskupa Alessandrii  22.10.1720</li>
                <li>Ukada regu zakonn  1720</li>
                <li>Odosobnienie w kociele w. Karola  od 23.11.1720 do 1.01.1721</li>
                <li>Odosobnienie w kociele Trinita da Lungi  od 10 do 25 1721</li>
                <li>Pobyt przy kociele w. Stefana, potem z bratem Giovanni Battista  od 28.11.1721 do 22.02.1722</li>
                <li>Otrzymuje od papiea ustne pozwolenie na tworzenie zakonu  1725</li>
                <li>wicenia kapaskie w Rzymie  07.06.1727</li>
                <li>Pierwsza Msza w. w Castellazzo  w kociele w. Antoniego  sierpie 1727</li>
                <li>Obejmuje pierwsz pustelni na Monte Argentario  1737</li>
                <li>Przebywanie na Monte Argentario, fundacja pierwszej pustelni  1728</li>
                <li>Pierwsze publiczne luby zakonne Pawa i jego towarzyszy, przyjcie znaku Mki Paskiej na tunice 
                    1741
                </li>
                <li>Zatwierdzenie Zgromadzenia przez papiea  11.06.1741</li>
                <li>mier Pawa w klasztorze w Rzymie -18.10.1775</li>
                <li>Beatyfikacja przez papiea Piusa IX  01.05.1853</li>
                <li>Kanonizacja przez papiea Piusa IX  29.06.1867</li>
            </ul>

            <h2 class="small">Spucizna literacka w.&nbsp;Pawa&nbsp;od&nbsp;Krzya</h2>
            <ul>
                <li>
                    Relacja z wicze duchowych w Castellazzo (tzw. Dziennik)
                </li>
                <li>
                    mier mistyczna
                </li>
                <li>
                    Regua Zgromadzenia
                </li>
                <li>
                    Listy (znanych 2060)
                </li>
                <li>
                    Mka Jezusa Chrystusa w czterdziestu krtkich rozwaaniach (dzieo przypisywane)
                </li>

            </ul>
        </div>
    </div>
    </div>
</template>

<script>
    export default {
        name: "Paul"
    }
</script>

