ze, gdyby ci w klasztorze byo le, to zaraz wracaj do domu\".\n            ")]),_vm._v("\n                Izydor odpowiedzia matce  tak jak odpowiedziaby kady Flamandczyk:\n                "),_c('i',[_vm._v("\n                    \"Mamo, ja jestem z tych, ktrzy nie zawracaj z raz obranej drogi\".\n                ")])]),_vm._v(" "),_c('p',[_vm._v("\n                W klasztorze w Ere Izydor spotka si z miym przyjciem, cho by oniemielony, to jednak otoczono go trosk. Nowicjusze szybko go zaakceptowali. Po odbyciu czasu postulatu rozpocz nowicjat, przyjmujc imi Izydor od w. Jzefa. Nie by wyksztacony, nie ukoczy szk, ale pragn suy Bogu tak, jak potrafi, bdc bratem zakonnym. W nowicjacie budzia si w nim tsknota za rodzinnym domem, pisa wiele listw do domu, ale ten czas by take dla niego czasem oczyszczenia i prby.  Musia take nauczy si jzyka francuskiego. Mistrz nowicjatu , cho wymagajcy,  okazywa mu jednak duo serca i yczliwoci.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                W roku 1908 zoy luby zakonne, ktre nastpnie co roku ponawia. Od tego momentu by przenoszony do kilku klasztorw: Wezembek- Oppem, do Courtrai, do Ere, w tym czasie peni rne obowizki, by kucharzem, ogrodnikiem furtianem, zawsze wypenia swe obowizki w pokorze i to w trudnych nieraz warunkach wojennych. W roku 1911 pojawia si u niego gangrena oka, na skutek czego musia podda si operacji. Lekarz zapowiedzia jednak przerzuty nowotworu w przyszoci. Dugo nie czeka na kolejn chorob. W 1916 r. by znowu chory i to na tyle, ze nie mg wykonywa swych obowizkw, a przecie mia zaledwie 35 lat! By to rak ukadu trawiennego. Izydor przyjmowa ten wyrok spokojnie tak, jak i poprzednie niemie wiadomoci. Jego pokor, posuszestwo i zgoda na rne trudnoci wynikajce z surowoci ycia,  surowych i niezrozumiaych polece przeoonych oraz choroby wida byo w jego licznych listach do rodziny. Wszystko przyjmowa jako wol Bo. Przed sw mierci zachowa pogod duch, czym budowa swych wspbraci. Umar cicho z modlitw na ustach 19