rawia Msz w. prymicyjn w rodzinnym miecie. A\n                tym czasem jego ojciec nie ustpuje, widzi go jako ksidza diecezjalnego. Wincenty dalej pogbia\n                swe studia i wci pragnie przywdzia habit zakonny w zakonie Pasjonistw. Ucieka z domu i udaje si\n                do nowicjatu , przywdziewa po 3 dniach habit stawiajc rodzicw przed faktem dokonanym. To oburza\n                ojca, ktry czyni kolejne kroki, aby temu przeszkodzi. Co robi? Pisze skarg do w. Pawa od Krzya\n                i da oddania syna! Potem z kolei ucieka si do protekcji u kardynaa Oddi, ktry dla prby wysya\n                innego ksidza z poleceniem modego Strambiego. Mody ksidz Strambi jednak tak gorliwie przedstawi\n                pikno ycia zakonnego wysannikowi kardynaa, e mao co a ten nie wstpiby rwnie do\n                Pasjonistw!\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Ojciec Wincentego podda si woli Boej. W 1769 r. Wincenty Maria Strambi zoy luby. W zakonie\n                okaza si wielkim misjonarzem, przeoonym klasztorw, Prowincji, a w kocu w 1801 r. Otrzymuje\n                nominacj na biskupa diecezji Tolentino i Maceraty w Italii. By biskupem niezwykym. Jako zakonnik\n                lubowa ubstwo i jako biskup dalej je przestrzega, wspomagajc innych ubogich, biednych . sta\n                si prawdziwym ojcem ubogich, a jednoczenie czowiekiem modlitwy. Pod koniec swego ycia, majc 78\n                lat usuguje w Paacu Kwirynalskim papieowi Leonowi XII. Gdy ten ciko zachoruje, wtedy skada\n                Bogu swe ycie jako ofiar w zamian za ycie papiea. Rzeczywicie, po kilku dniach papie\n                zdrowieje, a bp Strambi, Pasjonista umiera. Pochowany zosta w pobliu Ojca Zaoyciela w Bazylice\n                witych. Jana i Pawa w Rzymie.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                W 1957 ciao biskupa przeniesiono do Katedry w. Juliana w Maceracie. Beatyfikowa go 26 kwietnia\n                1925 papie Pius XI, kanonizowa 11 czerwca 1950 Pius XII. Wspomnienie liturgiczne na terenie Woch\n                obchodzone jest 1 stycznia. W kalendarzu wasnym zgromadzenia pasjonistw wspomnienie obchodzone\n                jest 24 wrzenia.\n            ")])])])])}]
