e garnli si do modego, gorliwego kapana szukajc w nim ostoi na przetrwanie tragicznych chwil.\n                Ojciec Bernard powiela wic modlitwy do w. Jzefa, Opiekuna w sytuacjach bez wyjcia, podawa udrczonym akty ufnoci i zawierzenia Bogu i Matce Pocieszenia, czczonej w rawskim kociele Pasjonistw. Rozpowszechnia swoje Dialogi z Ukrzyowanym.\n                Chcia suy tym najbiedniejszym i najnieszczliwszym, ktrzy si tumnie do niego zwracali. Rozumia, e kapan winien by-jak jego Mistrz   Hosti.\n                W tym czasie zosta dyrektorem, czyli bezporednim przeoonym, wychowawc i ojcem duchownym klerykw-pasjonistw. Pomidzy nim, a wychowankami bya niewielka rnica wieku, on sam by kapanem zaledwie od dwch lat.\n                Dzi duo si mwi i pisze o jego programie wychowawczym, ktry sobie nakreli na kartach swego Pedagogicum. Jest ono wiadectwem jego wielkiej dojrzaoci duchowej i arliwej troski, by powierzon sobie modzie wychowa do witoci.\n                Cho ble gowy si wzmagay, gosi kazania. Jego sowa dziaay cudown moc Boej aski, a nikt si nie domyla, e z zawrotem gowy wchodzi na ambon.\n                Cofanie si wojsk niemieckich w 1945 r. zamieniao Raw w gruzy, gdy uciekajcych okupantw bombardoway radzieckie samoloty. Koci i klasztor ojcw Pasjonistw ocala, wic ludzie szukali schronienia w jego podziemiach.\n                Ojciec Bernard nie korzysta ze schronu, lecz wyszed na ulice i pola: umierajcych rozgrzesza, rannych zanosi do klasztoru, gdzie cay parter zamieniono na szpital.\n                Od stycznia do marca 1945 r. by w tym szpitalu kapelanem, pielgniarzem i kucharzem.\n                Wysany pieszo (rodkw lokomocji nie byo) do Przasnysza doszed po kilku dniach do klasztoru ojcw Pasjonistw, by wznowi ycie zakonne i duszpasterstwo.\n                Okupant zdewastowa wszystko: koci zamieniono na magazyn zboowy, klasztor na koszary, yjcych tam zakonnikw wywieziono do obozu w Dziadowie i zamordowano.\n                Ojciec Bernard zapracowany i wy