<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1 class="small">
                    Modlitwa do w. Pawa od Krzya
                </h1>
                    <p>
                        Chwalebny w. Pawle od Krzya, ktry, medytujc mk Jezusa Chrystusa, osigne tak wielki stopie witoci na ziemi i szczcie w niebie, a przepowiadajc j, ofiarowae wiatu najskuteczniejszy rodek zaradczy na wszelkie istniejce zo, wyjednaj nam ask ustawicznego zachowywania wyrytej w naszym sercu pamici o Mce Chrystusa, abymy mogli zebra te same owoce tu na ziemi, jak i w wiecznoci. Amen.
                    </p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Prayer1"
    }
</script>

