<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1 class="small">
                    Modlitwa za wstawiennictwem witego Pawa od Krzya
                </h1>
                <p>
                    O chwalebny wity Pawle od Krzya, przez Boga zostae wybrany, aby zawiadczy wobec caej ludzkoci o gorzkich cierpieniach Jego Jednorodzonego Syna i aby po caym wiecie rozgasza naboestwo Mki Jezusa.
                </p>
                <p>
                    Przez Twoja nauk i wity przykad, Jezus nawrci tysice grzesznikw, gdy przyniose ich do stp Krzya, aby aowali za swoje przewiny i w ten sposb otrzymae dla nich Jego nieskoczone miosierdzie i odpuszczenie grzechw! Chwaa niech bdzie Jezusowi za Jego niewypowiedzian ask, ktra tak czsto objawiaa si w Twoim yciu i za tyle cudw ktre On zdziaa przez Ciebie dla nawrcenia dusz!
                </p>
                <p>
                    O bogosawiony wity Pawle od Krzya, zwracam si teraz do Ciebie aby ze swojego miejsca, obok Jezusa i Maryi w Niebie, spojrza miosiernie na moj biedna dusz i usysza moje modlitwy, i aby razem ze swoj mioci pokornie przedstawi je Jezusowi (wymie prob).
                </p>
                <p>
                    Prosz, wybagaj take dla mnie wielk mio do cierpienia Jezusa tak, abym przez czst medytacj Jego Mki mg ponie mj wasny krzy i ze witym poddaniem si zaakceptowa cierpienia, ktre Bg dopuszcza w moim yciu. Pom mi cierpie i powica si w cznoci z Jezusem dla nawrcenia mojej ndznej duszy, dusz drogich mojemu sercu i wszystkich ludzi. Pom mi kocha Jezusa i Maryj caym moim sercem, orduj za mn, abym przez ask Boga, umar w asce uwicajcej i dostpi wiecznej niebiaskiej radoci z obcowania w bogosawionej Obecnoci Jezusa i Maryi.
                </p>
                <p>
                    <i>Jedno Ojcze Nasz, Zdrowa Maryjo, Chwaa Ojcu.</i>
                </p>
                <p>
                    Prosz o to w Imi Jezusa Chrystusa Pana Naszego. Amen.</p>
                <p>
                    - Niech Mka Chrystusowa bdzie zawsze w naszych sercach! w. Pawle od Krzya, mdl si za nami!
                </p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Prayer2"
    }
</script>

<style scoped lang="scss">
    @import "../assets/scss/style";

</style>



