akonny w dniu 14. Listopada 1801 roku. Czy byy wahania? Tak! Mody Wawrzyniec Salvi przed rozpoczciem nowicjatu odczuwa pokusy rezygnacji. Przychodziy myli o tym, e w zakonie bdzie bardzo nieszczliwy.  Pokusa bya tak mocna, e si zaama. Wyjawi z paczem tajemnic mistrzowi nowicjatu, a ten obdarzy go pociech, tak, e  dozna uspokojenia i radoci.  \"Pokus pokonae, poniewa j ujawnie\", powiedzia mistrz.  20 listopada 1802 r.  Wawrzyniec zoy pierwsze luby zakonne , a take sporzdzi  \"Akt oddania si Jezusowi i Maryi Jego Matce\" By czcicielem Maryi, dlatego te przed rozpoczciem nowicjatu, nie zmieni , tak jak wszyscy, swego imienia, ale doda do swego imi Maria. Wawrzyniec Maria  po ukoczeniu studiw teologicznych w Rzymie otrzyma wicenia kapaskie w 1805 r, a potem specjalizowa si w kaznodziejstwie pod kierunkiem w.  Wincentego Strambi. ")]),_vm._v(" "),_c('p',[_vm._v("\n                Potem przyszy trudne czasu dla zakonw. Napoleon wyda dekret zmuszajcy zakonnikw i ksiy do skadania przysigi  wadzy okupacyjnej. Wielu z nich tego nie chciao uczyni, tak byo z modym Wawrzycem. Przebywajc w ukrycie w jednym z klasztorw peni rol nauczyciela wieckiego w maej miejscowoci koo Loreto.  Nasta rok 1814, reorganizacja ycia zakonnego, zatwierdzanie ponowne zakonw przez papiea, jednym z pierwszych byli Pasjonici. Wawrzyniec Maria Salvi rozpocz sw dziaalno jako misjonarz, rekolekcjonista i kaznodzieja. Szczegln czci darzy Dziecitko Jezus. Dlaczego jako Pasjonista darzy szczegln adoracj Dziecitko Jezus? Poniewa , gdy zachorowa miertelnie, ukazao mu si Dziecitko Jezus, ktre przywrcio mu zdrowie i polecio, aby rozpowszechnia naboestwo do Jego Dziecistwa. Odtd wiele gosi kaza o dziecitku a take sam wozi ze sob figurk Dziecitka Jezus, istniej dowody, e poprze ten wizerunek dokonywa cudw. Zosta synnym misjonarzem , kaznodziej, kierownikiem duchowym, nawet kardynaw.  By zawsze ruchliwy, gorliwym spowiednikiem, prowadzi liczn korespondencje ze swymi penitentami, . traktowa czas jako dar Boy. Zawsze zachowywa gorliwo w modlitwach zakonnych, rozmylaniu pomimo wiele podry, prac apostolskich, misji i rekolekcji. By przeoonym klasztoru oraz konsul torem prowincjalnym.Za  przyczyn jego modlitwy dokonyway si uzdrowienia, a take ustpienie epidemii.  Zmar w opinii witoci  w drodze d posugi duszpasterskiej w jednym z domw przyjaci zakonu  12 czerwca 1856. O wielkiej czci do jego osoby wiadczy fakt , i w momencie jego mierci ludzie  ogoocili jego szcztki ,  zabierajc rne rzeczy  ( kawaki habitu, wosy, krzy, raniec, figurk Dziecitka Jezus) traktujc je jako wite relikwie.   Po jego mierci odnotowywano kolejne  uzdrowienia za jego wstawiennictwem  Jan Pawe II  dokona beatyfikacji Wawrzyca Marii Salvi dnia 1 padziernika 1989 roku.\n            ")])])])])}]
