<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                    w. Gabriel Possenti<br />
                    (1838 - 1862)
                </h1>
                <img src="../assets/images/saints/gabriel-possenti.jpg" alt="">
                <p>Mia na imi Franciszek, a to dlatego, e pochodzi z Asyu , miejsca znanego od innego Franciszka.  Pochodzi ze znanej i szanowanej rodziny, ktr mona okreli dzisiaj jako elit spoeczn Pastwa Kocielnego. Urodzi si 1 marca 1838 r. w jednym z najokazalszych gmachw Asyu, poniewa ojciec by wtedy gubernatorem tej prowincji. Wszyscy byli z Franciszka dumni, i rodzice i nauczyciel, i ksia Jezuici, ktrzy pragnli go widzie w swoim zakonie. Jemu jednak nie w gowie byy cige modlitwy i pokuta, ale tace, humor, wesoo, popularno wrd kolegw. Stawa si bywalcem wielu uroczystoci zapraszany na rne przyjcia. Zawsze jednak modli si w domu na racu, tak jak nauczyli go rodzice. Momenty gbszego zastanowienia nad swoim yciem przychodziy w trudnych chwilach, kiedy zmara jego matka, a potem jego siostra w czasie epidemii. Podczas procesji obrazu Maryi dozna ogromnego wzruszenia i wtedy postanowi w sercu wstpi do jakiego klasztoru. Nie wszyscy byli z tego radzi. Ojciec szykowa dla niego dobr parti i kiedy dowiedzia si o zamiarach syna, wtedy zaprotestowa. Modlitwa Franciszka skruszya jednak serce ojca, ktry w kocu obieca mu swe bogosawiestwo. Jeszcze na dzie przed odjazdem do klasztoru Pasjonistw wzi udzia w ostatnich swych popisach, odbierajc w kolegium w Spoleto dyplom najlepszego ucznia oraz biorc udzia w przedstawieniu. Kt mg przypuszcza , e ten wytworny i elegancki modzieniec, wystrojony tak, e oczy dziewczt za nim kryy, na drugi dzie mia to wszystko odrzuci i zamkn si w surowym zakonie? Jednak tak si stao! Gdy skoczy szko redni wstpi do Pasjonistw.</p>

                <p>
                    W listach do rodziny pisze o spokoju, jaki odnalaz w klasztorze. Podejmowa heroiczne akty wyrzeczenia i pokuty, stawia sobie bardzo wysokie wymagania, bo chcia, eby jego ycie zakonne byo doskonae.
                </p>
                <p>
                    Chcia czyni tylko dobro, caego siebie odda Bogu. Najgorcej chcia kocha Matk Bo Bolesn, ktr wzi sobie za patronk. Przygotowywa si do wice kapaskich. Jednak zapad na zdrowiu.
                </p>
                <p>
                    Choroba okazaa si powana. Bya to nieuleczalna wwczas grulica. Zmar spokojnie 27 lutego 1862 r. majc zaledwie 24 lata.
                </p>
                <p>
                    W grskim klasztorze w Isola del Gran Sasso wyroso ogromne sanktuarium odwiedzane corocznie przez setki tysicy pielgrzymw, gwnie modziey woskiej.
                </p>
                    <p>
                        Gabriel zaledwie 6 lat ycia spdzi w zakonie, gdy umar na grulic, ale ten czas wystarczy do tego, aby uznano go za wit osob. Sta si zakonnikiem wiernym w wypenianiu Reguy i innych przepisw. Ukocha Maryj Bolesn, przyj imi Gabriel i marzy o tym , by by misjonarzem, kaznodziej , apostoem Maryi. Jego wrodzona wesoo i humor nie znikny, nawet wtedy gdy ju chory lea w ku. Zawsze peen gorliwoci i mioci do Jezusa Ukrzyowanego i Jego Matki Bolesnej. Jest patronem modziey woskiej i zakonnej. Co roku w czasie wakacji do jego sanktuarium w Teramo (San Gabriele) zjedaj si bardzo liczne grupy modych ludzi.
                    </p>
                <p>
                    Jego kult potwierdzi Pius X 14 maja 1905, a nastpnie beatyfikowa go 31 maja 1908. Uroczystoci odbyy si najpierw w Watykanie, w obecnoci kardynaw, pniej w Rzymie wrd zaproszonych pasjonistw i przedstawicieli wszystkich prowincji zgromadzenia. Wrd goci znaleli si m.in. kierownik duchowy i spowiednik w. Gabriela, o. Norbert, CP oraz cudownie uzdrowiony za wstawiennictwem witego, Signor Dominico Tiberi.
                </p>
                <p>
                    Wol Leona XIII i Piusa X byo, aby pasjonici wspominali b. Gabriela 31 maja.
                </p>
                <p>
                    Kanonizacji b. Gabriela dokona Benedykt XV 13 maja 1920 roku.
                    Ks. Jan Twardowski napisa o nim wiersz, w ktrym  czytamy m.in <i>"(...) O Gabrielu bledziutki, z bolesnym w rku obrazkiem;
                    jeste mi cay - gdy kocham.  Szczliwym wynalazkiem."
                </i>

                </p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Gabriel"
    }
</script>

<style scoped lang="scss">
    @import "../assets/scss/style";

</style>


