<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                    B. Bernard Maria Silvestrelli<br/>
                    (1831 - 1911)
                </h1>
                <img src="../assets/images/saints/bernard-maria-silvestrelli.jpg" alt="">
                <p>Cezary Silvestrelli by synem arystokraty rzymskiego i szlachcianki toskaskiej, wyrs na postawnego
                    modzieca, przystojnego, dobrze zbudowanego, wyksztaconego, wic budzi szacunek i podziw w
                    towarzystwie wyszych sfer. Niejedna panna pragna go za ma, poniewa pochodzi ze znamienitego
                    rodu. Rodzice planowali dla niego karier pastwow , a jeli ju w stanie duchownym , ot kocieln.
                    Opatrzno boa pokierowaa inaczej. Modu Silvestrelli wybra si z przyjacimi na polowanie.
                    Przygoda owiecka wcigna ich do tego stopnia, e zgubili drog, trafili do pobliskiego Soriano,
                    do klasztoru Pasjonistw, z ktrymi bya blisko zwizana rodzina towarzysza przygody. Utrudzenie,
                    godni , zmorzeni zadzwonili do furty klasztornej, przyjto ich serdecznie, nakarmiono i pooono
                    spa. Ale w nocy zaszo co niespodziewanego. Obudzi ich gos dzwonw i szmer idcych korytarzem
                    zakonnikw. Potem da si sysze piew modlitwy zakonnej, nastpnie zakonnicy powrcili do swych
                    cel i rano ponownie si modlili w kaplicy. Silvestrelli od pnocy nie mg ju zasn, nie dawaa
                    mu spokoju myl przepeniajca go radoci - czy moe by co pikniejszego ni spdzenie swego
                    ycia w ubogim klasztorze , w modlitwie, pokucie, zamiast doznawa wiatowych przyjemnoci w
                    bogactwie i przygodach? Tu jest prawdziwe niebo na ziemi. Nazajutrz przy odjedzie powiedzia
                    furtianowi "Ten dom jest prawdziwym rajem na ziemi!".</p>
                <p>
                    W 1853 r. zosta beatyfikowany Zaoyciel Pasjonistw, mody Cezary bardzo Odprawi rekolekcje w
                    rzymskim klasztorze Pasjonistw, podczas ktrych podj decyzj wstpienia do tego zakonu. Rodzice
                    byli jednak przeciwni. przey to wydarzenie. Cezary przedstawi wiadectwo moralnoci i zosta
                    jednak przyjty. Rozpocz nowicjat w domu na Monte Argentario. Przyj habit zakonny, ale ju
                    wkrtce zacz chorowa. Zbyt przesadnie potraktowa praktyki zakonne, dlatego musia opuci
                    nowicjat. Zwrci si jednak do Generaa zakonu by mg dalej przebywa w murach klasztoru i
                    studiujc teologi ,przygotowywa si do kapastwa. Pozwolono mu woy sutann.
                </p>
                <p>
                    Kiedy zosta kapanem , musia zdecydowa co dalej? Albo ponownie wstpi do zakonu, albo przej do
                    diecezji. Wybra to pierwsze, za rad biskupa poprosi ponownie o przyjcie do Pasjonistw. Wiosn
                    1856 r . Cezary Silvestrelli przyj ponownie habit zakonny i na zawsze ju zosta Pasjonist. W
                    nowicjacie odznacza si szczegln witoci, mia za wspbrata modego Gabriela Possenti, ktry
                    potem w wieku 24 lat zosta witym.
                </p>
                <p>
                    Sam Cezary, ju bernard Silvestrelli po nowicjacie uzupeni studia, zosta dyrektorem klerykatu,
                    peni rne wane funkcje w zakonie , a w kocu przez 5 kadencji wybierany by na Generaa zakonu.
                    Za jego kadencji zakon rozszerzy sw dziaalno: liczba zakonnikw wzrosa dwukrotnie a klasztory
                    Pasjonistw powstaway w krajach europejskich i w Ameryce. Papie stara si mianowa go kardynaem, ale ten w pokorze tej funkcji nie chcia przyj.
                    Bernard Silvestrelli dugoletni Genera i krzewiciel zakonu zmar w 1911 r. w klasztorze w Moricone.
                    Zosta beatyfikowany przez Jana Pawa II w 1988 r.
                </p>
            </div>
        </div>
    </div>
