<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                   Suga Boy<br />o. Bernard Kryszkiewicz<br />
                    (1915  1945)
                </h1>
                <img src="../assets/images/saints/bernard-kryszkiewicz.jpg" alt="">
                <p>Urodzi si w Mawie na Mazowszu. Ukoczywszy dziesi lat zacz  wedug praw wczesnego szkolnictwa  chodzi do gimnazjum.
                    Przedtem pobiera prywatne lekcje w domu i wiele wybrykw uszo mu pazem.
                    Teraz nie umia si podda szkolnemu rygorowi i w trzeciej klasie trzy dwje z polskiego, matematyki i geografii  skazay go na repetowanie.
                    Wwczas matka postanowia go odda w stanowcze, mskie rce ojcw Pasjonistw w Przasnyszu, ktrzy tam prowadzili gimnazjum zakonne z internatem.
                    Po czterech latach spdzonych w klasztornym internacie wrci po raz pierwszy do domu, z decyzj wstpienia do nowicjatu ojcw Pasjonistw.
                    Gdy mia dziewitnacie lat, zoy luby zakonne i rozpocz studia filozoficzno-teologiczne, ktre kontynuowa w Rzymie. Tam te (przed ich ukoczeniem) otrzyma wicenia kapaskie 4 lipca 1938 r.
                    Ojciec Bernard ukocha Chrystusa i chcia si dla Niego wypali w swym kapaskim posugiwaniu. Uporczywe ble gowy skoniy przeoonych do odesania go do Polski.
                    Wrci w grudniu 1938 roku, a w lutym 1939 r. obj swoj pierwsz placwk duszpastersk w Rawie Mazowieckiej. Kilka miesicy pniej zosta przeniesiony do Przasnysza.
                    Po wybuchu drugiej wojny wiatowej, po ewakuacji klasztoru w Przasnyszu wrci do Rawy Mazowieckiej.
                    By to czas nasilenia niemieckich apanek i wywozw na przymusowe roboty do Niemiec, czas aresztowa i rozstrzeliwa; ludzie garnli si do modego, gorliwego kapana szukajc w nim ostoi na przetrwanie tragicznych chwil.
                    Ojciec Bernard powiela wic modlitwy do w. Jzefa, Opiekuna w sytuacjach bez wyjcia, podawa udrczonym akty ufnoci i zawierzenia Bogu i Matce Pocieszenia, czczonej w rawskim kociele Pasjonistw. Rozpowszechnia swoje Dialogi z Ukrzyowanym.
                    Chcia suy tym najbiedniejszym i najnieszczliwszym, ktrzy si tumnie do niego zwracali. Rozumia, e kapan winien by-jak jego Mistrz   Hosti.
                    W tym czasie zosta dyrektorem, czyli bezporednim przeoonym, wychowawc i ojcem duchownym klerykw-pasjonistw. Pomidzy nim, a wychowankami bya niewielka rnica wieku, on sam by kapanem zaledwie od dwch lat.
                    Dzi duo si mwi i pisze o jego programie wychowawczym, ktry sobie nakreli na kartach swego Pedagogicum. Jest ono wiadectwem jego wielkiej dojrzaoci duchowej i arliwej troski, by powierzon sobie modzie wychowa do witoci.
                    Cho ble gowy si wzmagay, gosi kazania. Jego sowa dziaay cudown moc Boej aski, a nikt si nie domyla, e z zawrotem gowy wchodzi na ambon.
                    Cofanie si wojsk niemieckich w 1945 r. zamieniao Raw w gruzy, gdy uciekajcych okupantw bombardoway radzieckie samoloty. Koci i klasztor ojcw Pasjonistw ocala, wic ludzie szukali schronienia w jego podziemiach.
                    Ojciec Bernard nie korzysta ze schronu, lecz wyszed na ulice i pola: umierajcych rozgrzesza, rannych zanosi do klasztoru, gdzie cay parter zamieniono na szpital.
                    Od stycznia do marca 1945 r. by w tym szpitalu kapelanem, pielgniarzem i kucharzem.
                    Wysany pieszo (rodkw lokomocji nie byo) do Przasnysza doszed po kilku dniach do klasztoru ojcw Pasjonistw, by wznowi ycie zakonne i duszpasterstwo.
                    Okupant zdewastowa wszystko: koci zamieniono na magazyn zboowy, klasztor na koszary, yjcych tam zakonnikw wywieziono do obozu w Dziadowie i zamordowano.
                    Ojciec Bernard zapracowany i wyczerpany czsto dra z zimna i godu, gdy ostatni kromk chleba dawa innym. W miecie z powodu godu i robactwa panowa tyfus.
                    Doniesiono mu o umierajcej kobiecie. Ojciec Bernard j wyspowiada i przewiz do szpitala, gdzie wkrtce zmara. Prawdopodobnie wwczas sam nabawi si tyfusu i znalaz si w szpitalu. Jego mody organizm zaciekle zmaga si z chorob.
                    Zebrani przy konajcym zakonnicy, personel szpitalny i ktokolwiek mg si bliej docisn byli wiadkami jak odchodzi pogodny i wdziczny Bogu za trzydzieci lat danego mu ycia, za powoanie i ask kapastwa.
                    Syszano jak si modli: <i>Panie Jezu, nie lkam si mierci, lecz pragn jak najszybciej spocz w Twych objciach.</i> Przywar ustami do podanego mu krzyyka, szepczc: <i>Jezu, kocham Ci.</i>
                    Bya to pierwsza sobota miesica, 7 lipca 1945 roku.
                </p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "BernardKryszkiewicz"
    }
</script>

