<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                    w. Maria Goretti<br />
                    (1890 - 1902)
                </h1>
                <img src="../assets/images/saints/maria-goretti.jpg" alt="">
                <p>Nazwana przez papiea Piusa XII w. Agnieszk XX wieku, urodzia si na wsi pod Ankon we Woszech w roku 1890, jako trzecia z siedmiorga dzieci.</p>
                    <p>
                        Bya, jak okrelia jej matka, dziewczynk szczliw, dobr, szczer i uczuciow, bez kaprysw, powan i zawsze posuszn.
                    </p>
                <p>
                    Jej ojciec umar, gdy miaa dziewi lat. Maria opiekowaa si modszym rodzestwem, pomagaa w pracach domowych, podczas, gdy jej matka bya zajta przy gospodarstwie.
                </p>
                <p>
                    Pierwsz Komuni w. Maria przyja ukoczywszy jedenacie lat i odtd ze wszystkich si dya, by co dzie by lepsz.
                </p>
                <p>
                    Sze miesicy pniej bohaterska dziewczynka zostaa poddana cikiej prbie.
                </p>
                <p>
                    Rodzina Goretti dzielia dom ze wsplnikami ojca Marii, Aleksandrem, niegodziwym modziecem, ktry zacz przeladowa dziewczynk sw grzeszn namitnoci.
                </p>
                <p>
                    Odepchna go natychmiast, ale nie poskarya si nikomu, gdy Aleksander zagrozi, e zabije j i jej matk, jeli go wyda.
                </p>
                <p>
                    Nie zrezygnowa jednak ze swego podania i drugi raz gwatownie zaatakowa Mari. wita znowu mu si opara ze wszystkich si krzyczc: Nie, to jest grzech. Bg nie chce tego.
                </p>
                <p>
                    Aleksander, zdjty strachem i gniewem, zacz uderza j na olep sztyletem. Czternacie ciosw przeszyo jej ciao.
                </p>
                    <p>
                        Maria zostaa przewieziona do szpitala w Nettuno. Chirurdzy pracowali gorczkowo nad uratowaniem jej ycia, ale wkrtce okazao si, e nic nie mona zrobi. Nastpnego ranka, przyja Komuni w.
                    </p>
                <p>
                    Zapytana o Aleksandra, odpowiedziaa wyranie, e mu przebaczya, e bdzie si za niego modlia i e chciaaby go zobaczy w niebie. 6 lipca 1902 roku dziewczynka umara. 27 lipca 1950 roku ogoszono j wit.
                </p>
                <p>
                    Po raz pierwszy w historii Kocioa dziao si to w obecnoci rodziny kanonizowanej. Modlitwy Marii w intencji jej mordercy zostay wysuchane. W smym roku pobytu w wizieniu Aleksander cakowicie si odmieni.
                </p>
                <p>
                    Zwolniony po dwudziestu siedmiu latach za dobre sprawowanie, pospieszy matk witej baga o przebaczenie.
                </p>
                <p>
                    Potem wstpi do zakonu. By wiadkiem w procesie kanonizacyjnym Marii. Dane mu byo rwnie uczestniczy w ceremonii jej kanonizacji.
                </p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "MariaGoretti"
    }
</script>

<style scoped lang="scss">
    @import "../assets/scss/style";

</style>


