            <div class="page__content">
                <h1 class="small">
                    Myli w. Pawa od Krzya
                </h1>
                <p>
                    <i>
                        Bd szczliwy, gdy idziesz ladami Jezusa; bd "wyszydzany i pogardzany przez ludzi"; bd
                        ukrytym przed oczyma wiata i czy wszystko czego da Bg.
                    </i>
                </p>
                <span class="badge">
                    *
                </span>
                <p>
                    <i>
                        Cae ycie Jezusa byo Krzyem. Cae ycie tego, kto suy Bogu, winno by wic takie, by zosta
                        na Krzyu z Jezusem.
                    </i>
                </p>
                <span class="badge">
                    *
                </span>
                <p>
                    <i>
                        Mka Jezusa jest cakowicie dzieem Mioci. Nigdy nie powiniene straci tego z oczu.
                    </i>
                </p>
                <span class="badge">
                    *
                </span>
                <p>
                    <i>
                        Niech Mka Pana zawsze bdzie w twoim sercu.
                    </i>
                </p>
                <span class="badge">
                    *
                </span>
                <p>
                    <i>
                        Bd posuszny woli Boej. Pozwl Bogu, aby wrs w ciebie. Niech cae twoje ycie bdzie przez
