                    </i>
                </p>
                <span class="badge">
                    *
                </span>
                <p>
                    <i>
                        Jeli spojrzymy na trudnoci w naszym yciu, jak na cz umiowanego Boego planu i przyjmiemy
                        je za istniejce, gdy On dla nas ich pragnie, to pomoe nam lepiej Go pozna i pokocha.
