                        zupenie pogry si w Bogu, usysze i posmakowa sowa ycia wiecznego. III,745.
                    </i>
                </p>
                <span class="badge">
                    *
                </span>
                <p>
                    <i>
                        Czy wszystko, by ukry si w ranach Jezusa. W ten sposb staniesz si bogatym we wszystko, co
