                        Niech wszystkie stworzenia prowadz ciebie do Mioci Boga.
                    </i>
                </p>
                <span class="badge">
                    *
                </span>
                <p>
                    <i>
                        Najwikszym darem od Pana jest zdolno pokochania caym sercem tych, ktrych uwaamy za
