                        wzrocie w witoci. II,296
                    </i>
                </p>
                <span class="badge">
                    *
                </span>
                <p>
                    <i>
                        Przyjaciele biednych s najbardziej ukochanymi przyjacimi Boga.
                    </i>
                </p>
                <span class="badge">
                    *
                </span>
                <p>
                    <i>
