                        -fizyczne i duchowe cierpienia;<br/>
                        -spustoszenia i niekoczca si jaowo;<br/>
                    </i>
                </p>
                <span class="badge">
                    *
                </span>
                <p>
                    <i>
                        Poniewa "wszystkich, ktrzy chc pobonie y w Chrystusie Jezusie, spotkaj przeladowania".
