                        naprawd, czowiek moe zobaczy, czy jest cierpliwy, pokorny i bezinteresowny.
                    </i>
                </p>
                <span class="badge">
                    *
                </span>
                <p>
                    <i>
                        Czasami na jaki okres, Bg odbierze tym, ktrzy Mu su, wszystkie aski pocieszenia. Czyni
                        to, aby uczy ich jak maj suy czyst mioci i t drog oni staj si naprawd wiernymi
                        Jego sugami.
                    </i>
                </p>
                <span class="badge">
                    *
                </span>
                <p>
                    <i>
                        Jezus modli si trzy godziny na Krzyu. Bya to prawdziwa modlitwa krzyowa, pozbawiona
                        wszelkiej pociechy z jakiejkolwiek strony. Jake to wspaniaa lekcja!
                    </i>
                </p>
                <span class="badge">
                    *
                </span>
                <p>
                    <i>
                        Chc mwi o wielkich rzeczach, ale tylko jedynie ten, ktry kocha, wie jak mwi o mioci.
                        Mio sam uczy nas jzyka.
                    </i>
                </p>
                <span class="badge">
                    *
                </span>
                <p>
