<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                    B. Dominik Barberi<br />(1792 - 1849)
                </h1>
                <img src="../assets/images/saints/dominik-barberi.jpg" alt="">
                <p>Dominik urodzony by w Viterbo w wczesnym Pastwie Papieskim, w rodzinie chopskiej ale nie ubogiej. Jego ojciec, Giuseppe zmar wczenie, zostawiajc on z licznym potomstwem, najmodszy Dominik mia wwczas 4 lata. Matka, Maria Antonina, kobieta pena prostoty  i gbokiej wiary nauczya swych dzieci take radoci ycia i zawierzenia Bogu. Zmara gdy Dominik mia 11 lat. Musia rozsta si z domem rodzinnym, dzieci zostay rozdzielone pomidzy krewnych. Brat rodzinny Dominika, Salvatore, zapragn wie ycie pustelnicze, zaoy pustelni w pobliu dawnego domu i usiowa nakoni Dominika do przyczenia si. Skoczyo si jednak na tym e porzuci sw pustelni i wstpi do pasjonistw. Dominik trafi pod opiek ciotki, ktra liczya na jego pomoc w duym gospodarstwie. Maestwo Pacelli pokochali swego nowego syna i mieli du pociech z jego pomocy. Jednak zabrako w tym czasie edukacji. Samo pisanie i czytanie nie wystarczyo , aby potem zosta misjonarzem. Sam zacz czyta Pismo wite, ywoty witych , uczy si nawet tekstw na pami, jednak gwnym zajciem Dominika Berberii by wypas owiec. Ju w majc 14 lat Bg udzieli mu aski modlitwy kontemplacyjnej i odtd poczo go trawi pragnienie nawracania niewierzcych i troski o inn owczarni, owczarnie Chrystusa. </p>

                <p>
                    Dorastajc przechodzi take kryzys wiary, czytywa ksiki o wielkich wyprawach, o bohaterach staroytnej Grecji, wic pobono religijna wydawaa mu si maostkowa i niepotrzebna. Brako take rady mdrej osoby, kierownika duchowego, kogo kto mgby mu wiele wytumaczy.
                </p>

                <p>
                    Po raz pierwszy spotka pasjonistw przypadkiem w Vetralla, gdy mia 15 lat. Przyszy czasy napoleoskie, wiele zakonw zostao likwidowanych, take nasi ojcowie musieli opuszcza klasztory. Jeden z nich , o. Giuseppe, schroni si w domu rodzinnym w ssiedztwie zagrody, gdzie mieszka nasz chopak. Doszo do spotka,  skoczyo si na przyjani i czytaniu wielu ksiek z biblioteki Pasjonistw. O. Giuseppe zosta jego kierownikiem duchowym, uczy go modlitwy, medytacji o Mce Chrystusa, Dominik zacz regularnie uczszcza do spowiedzi  i komunii w. przyszy jednak chwile trudne. Napoleon szykowa si do wojny z Rosj, wic potrzebowa nowych onierzy. Dla Dominika by to wiek poborowy, wic trwoc si i bojc silnych mrozw moskiewskich czeka na losowanie poborowych wrd wieniakw woskich. Wwczas to nawiedzi go dziwny sen. Ujrza sw matk , ktra przekonaa go  do ufnoci Bogu i zachcia do zapisania si do Bractwa Racowego. Jeli to uczyni, wycignie taki numer w losowaniu by unikn poboru. Tak te uczyni. Na dzie przed losowaniem uklkn  jak zwykle i zacz si modli , rozmyla o Mce Paskiej. W czasie tej dugiej modlitwy przyszo natchnienie - by zoy warunkowy lub- jeli uniknie  poboru i jeli kasata zostanie zniesiona, wstpi do Pasjonistw. Istotnie modlitwa Dominika zostaa wysuchana Mody Barberi wycign jeden z najwyszych numerw i zwolniony z poboru.
                </p>

                <p>
                    Przysza kolejna prba - majc 20 lat zakocha si w dziewczynie! tym bardziej ,i wujostwo, liczc na oddanie siostrzecowi gospodarstwa, razem z proboszczem i z jego polecenia wybrali t dziewczyn na przysz on Dominika i gospodyni. Dominik istotnie kocha prostym sercem urocz i pracowit dziewuszk, ale co ze zoonym przyrzeczeniem ? udzi si faktem, e kasacja zakonw nie zostaa odwoana. Zarzuci modlitw myln, idc jednak na randki czsto odmawia raniec.  Myla o oenku. Ojciec Giuseppe , pasjonista nakania do zakonu , inni spowiednicy, ktrych si radzi,  opowiedzieli si za maestwem. Przysza rozterka, niepokj . czy byo co zego w mioci do dziewczyny o czystym sercu? Po jakim czasie wpad w chorob tak cik, e przyj namaszczenie chorych. To by czas oczyszczenia i wyrzutw sumienia. Ujrza siebie w modlitwie przed sdem ostatecznym, dozna trwogi, gdy wiedzia , ze nie wykorzysta wszystkiego, co Bg mu oferowa. Usysza gos , wezwanie do wybrania innej drogi. W widzeniu ukaza mu si Maryja, ktra rczya za Dominika przed swym Synem.
                </p>
                <p>
                    Od tego czasu zacz si wicej modli, cho targany cigle wtpliwociami , wychodzi powoli z etapu wtpliwoci i rozterki. Ponownie zacz przystpowa regularnie do Komunii w. , czujc jak napenia go si. Ponownie przyszed  glos powoania, coraz bardziej wyrany i syszalny w sercu. Musia zerwa zarczyny, dziewczyna przyja te wiadomo z wielkim smutkiem i z wielk godnoci. Przez jaki czas jeszcze przebywa w gospodarstwie ,  oczekujc na legalizacj zakonw. Jeden z Pasjonistw zaproponowa mu , by zosta wieckim bratem w Zgromadzeniu, a potem przyjtym do zakonu.  W 1813 r. Dominik Berberii ponownie otrzyma wizje wewntrzne. Bg objawi mu przysze zadania, ktrych miaby si podj jako pasjonista -  goszenie Ewangelii  zbkanym owieczkom.  By to decydujcy moment. Podda si cakowicie woli boej - nie ukoczywszy szkoy, zacz sam uczy si aciny, doznawa przey mistycznych i tskni za yciem zakonnym.  Rok 1813 to koniec przeladowa Kocioa. Dominik najpierw jako brat wiecki, potem  nowicjusz rozpocz sw dug drog w zakonie Pasjonistw. Nadrobi nauk, odby studia, zosta wykadowc filozofii, opiekunem studentw a w kocu  jako kapan zosta wysany do Anglii , kraju wrogiego stolicy Apostolskiej. Jego dziaalno przyniosa plon obfity, przyj do kocioa Rzymskokatolickiego wielu anglikanw, m.in. Johna H. Newmana, pniejszego kardynaa a take do zakonu Georgea Spencera, ktry by przodkiem synnej w XX w. ksinej Diany. Dzisiaj do dziedzictwa Dominika Barberi przyznaj si pasjonici w Belgii, Francji, Anglii i Holandii.  Papie Pawe VI beatyfikowa Go 27 padziernika 1963 roku.
                </p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "DominikBarberi"
    }
</script>

