var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"block"},[_c('div',{staticClass:"page"},[_c('div',{staticClass:"page__blur"}),_vm._v(" "),_c('div',{staticClass:"page__content"},[_c('h1',[_vm._v("\n                w. Karol Houben"),_c('br'),_vm._v("(1821 - 1893)\n            ")]),_vm._v(" "),_c('img',{attrs:{"src":require("../assets/images/saints/karol-houben.jpg"),"alt":""}}),_vm._v(" "),_c('p',[_vm._v("\n                Pochodzi z miasteczka prowincjalnego Munstergelen w Holandii. W maej wsi, znajdujcej si w  graniczcej z Niemcami Limburgii. By czwartym z dziesiciu dzieci mynarza. Mia pobonych i ciko pracujcych rodzicw -  to od nich odebra Karol podstawowe, chrzecijaskie wychowanie. Codziennie odmawia wraz z nimi poranne modlitwy , otrzymywa od nich  bogosawiestwo do pracy, i wraz z nimi wieczorem, na zakoczenie dnia odmawia raniec. By agodnym, prostym i pobonym  modziecem.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Poza  wyjtkow pobonoci, jedn z cech charakteru Andrzeja bya jego wyjtkowa wytrwao. Przez 10 lat chodzi z trudem dwumilow drog  do szkoy redniej w Sittard, tak , e wieniacy nie mogli powstrzyma si od komentarzy : czy ten gupi syn mynarza nigdy nie skoczy swej nauki? Ale Andrzeja nie  pokonay te trudnoci. Wyznaczy sobie cel do osignicia i z ponur determinacj charakteryzujc jego drog, hartowa si i pokonywa kad przeszkod. Celem  byo kapastwo. Od tak dawna jak tylko pamita, chcia zosta ksidzem. Nie pamita jednak, kiedy podj tak decyzj. Ale kiedy ju to uczyni, by nieugity w swym zamierzeniu. Nie mogy powstrzyma go nawet braki w inteligencji. Z pomoc Bo potrafi walczy i pokonywa ka niedoskonao.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Czsto zdarzao si, e gdy jego matka  odkadaa po pracowitym dniu na bok szycie lub cerowanie, i przed snem rozpalaa ogie na noc , Andrzej cigle jeszcze mczy si nad  swoimi wiczeniami. Stawaa wtedy nad nim i agodnie kadc rce na jego ramionach szeptaa: Andrzeju, synku, czy wszystkie te studia  nie mcz ci? Czy nie lepiej byoby odoy na bok  ksiki i pomc ojcu? Duo czasu upynie zanim ukoczysz twe studia.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Po szczeglnie cikim dniu w szkole, przychodzia pokusa, aby myle tak, jak matka. Po dugich i daremnych prbach rozwizania swych wielu problemw bywa zniechcony; Coroczne  wydatki, ktre rodzice z trudem pokrywali, byy powodem myli o rezygnacji ze swoich wyduonych studiw. Myl, e mgby by bardziej potrzebny swoim rodzicom, przy pracy w ojcowskim mynie lub na polu, czsto wydawaa mu si nie tylko sensowna i praktyczna, ale take wysoce odpowiednia.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Jego postanowienie cigle si chwiao ,ale podczas tych cikich lat,  zaowocowao to  w kocu   w jego przekonaniu  tym , e Bg powoujcy go do jakiej wielkiej pracy, oddaje mu z powrotem zaufanie i siln wol.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                W kocu jego matka bya tak o niego zaniepokojona , e zdecydowaa si skonsultowa z ksidzem parafialnym, ojcem Delaheye. Ksidz odrzek matce: Pozwl mu kontynuowa nauk. Ten chopiec bdzie wielkim czowiekiem.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Andrzej czsto spdza  w drodze z domu do szkoy dugie chwile na modlitwie przed Najwitszym Sakramentem. Nieraz wic ju zmierzchao, gdy wraca do domu. Jego koledzy mogli potwierdzi, e zna tylko dwie drogi w miasteczku: do szkoy i do kocioa. Czasami, gdy zbliaa si pora rodzinnego raca, a on si jeszcze nie pojawia,  matka coraz bardziej zaniepokojona  wysyaa  na poszukiwanie Andrzeja drugiego syna. Ten od razu kierowa si do kocioa i zaraz go odnajdywa, modlcego si przed Najwitszym Sakramentem.\n                Na wiosn 1840 r. otrzyma wezwanie do suby wojskowej. Mia 19 lat. Czekano na losowanie modych ludzi, ktrzy mieli trafi do wojska. 2 marca wylosowa nieszczliwy numer. Zosta wczony do I  go  Regimentu Pieszego i wpisany na lipiec.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Trafi w miejsce, gdzie musia znie wiele upokorze. Take jako katolik. Koledzy odnosili si do niego ordynarnie i grubiasko. Ale wszyscy zauwayli , e duo powica czasu w miejscowym kociele, niektrzy nawet patrzyli na niego z podziwem i respektem. Przyjmowa Najwitszy Sakrament w kad niedziel rano.\n                W wojsku odnalaz swe prawdziwe powoanie. Jeden z kolegw  niejaki Raaymakers, mia brata , ktry wstpi wanie do Pasjonistw w Ere, w Belgii. Andrzej nigdy nie sysza o tym zakonie, poniewa wszystkie  zostay skasowane w Holandii i nietolerowanie a do 1842 r.  Raaymakers by pod wraeniem, pokazywa Andrzejowi listy od swojego brata. Akurat by to arliwy okres nowicjatu dla jego brata, okres, w ktrym doznawa yciowego szczcia w zakonie. Andrzej by bardzo zainteresowany. To byo dokadnie to sposb na ycie, za ktrym tak tskni i dla ktrego tak ciko pracowa.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Dnia 9. Padziernika wrci do domu, poniewa jego ojciec zapaci po zbiorze niw kaucj za sub. Andrzej powrci do ksiek i nauki. Mia opnienia. Jeden z profesorw otworzy prywatn szko w Broek Sittard i tam Andrzej nadrabia studia. Stary nauczyciel  by zdumiony wybitn popraw ucznia, ktry z atwoci studiowa i nadgania program. Profesor Shyre relacjonowa: wygldao to tak, jakby rka Boga go dotkna . Okazao si , e ten profesor zna Pasjonistw w Ere i przez to przybliy mu ycie klasztorne, bardziej ni kolega z wojska. Jeszcze bardziej usysza, przekona si do tego, by zosta , idc za gosem Boga, czonkiem Zgromadzenia Pasjonistw. Rozmawia o tym ze swym ojcem. Matka zmara mu krtko przedtem. Jego ojciec radzi si ojca Delahaye, ktry natychmiast poradzi mu napisa do przeoonego klasztoru w. Michaa w Ere. Andrzej zosta przyjty do nowicjatu dnia 5. Listopada 1845 r. Gdy zblia si dzie odjazdu pojawiy si  znaki wewntrznej walki . Rozmawia z ojcem, ktry odradza mu tej decyzji , jednak Andrzej by zdecydowany. Swej siostrze Sybilli odpowiedzia: Dla sprawy Naszego Pana nic nie zrobi, poza tym, e chtnie spdz me ycie na pustyni. Noc przed odjazdem po spakowaniu bagay, po raz ostatni uklk wraz z rodzin do raca. Potem przeszed z pokoju do pokoju , zatrzyma si przed obrazem matki, i wtedy siostra Krystyna powiedziaa:  wiem co mylisz. Mylisz, e nigdy nie zobaczysz tych rzeczy wicej. Tak, droga, odpowiedzia.  Nastpnego ranka byy poegnania. Z ojcem, przy grobie matki. Ojciec pobogosawi go, a nastpnie Andrzej opuci cmentarz. W ponury dzie listopada 1845 r. w klasztorze Pasjonistw  blisko Tournai w Belgii, Andrzej Houben pojawi si po raz pierwszy. Bya to pierwsza fundacja o poza Itali. Barberi czyni przygotowania do dziaalnoci apostolskiej w Anglii. Przyjto Andrzeja z uprzejmoci i badawczym nastawieniem. Ujrzano modego czowieka, wysokiego, zbudowanego, smag posta i silne rysy twarzy. Mia dobr opini od proboszcza, o. Delahaye, ktry zapewnia o jego solidnych cnotach i mioci do modlitwy.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                W nowicjacie Andrzej czyci podogi, zamiata schody, obiera ziemniaki, my talerze po obiedzie i wykonywa wiele innych obowizkw. By te surowo karany za drobne nawet cakiem zmylone bdy. Ani onierz Raaymakers ani profesor Schyren nie ostrzegli go przed tym co miao go spotka. By jednogonie pochwalony przez ojcw na kapitule. Wdzia habit 1 dnia  grudnia. U Pasjonistw istnia zwyczaj zmiany imienia przyjtego na chrzcie, na inne, i mia to by wyraz wejcia w inny, wyszy stan ycia. W nowicjacie nadano mu imi Karol od w. Andrzeja.  W nowicjacie brat Karol by  zatracony w Bogu. O jego duchowej stronie ycia nie wiele wiemy. Nie zostawi adnych napisanych sw dotyczcych ycia duchowego. By maomwny, zamknity pod tym wzgldem. By jedn z tych dusz, ktre przez codzienny przykad niewiadomie oddziayway na innych. By dokadny w zachowaniu Reguy, prosty i przyjacielski w stosunkach z innymi, agodny i szczery, naturalnie miosierny, dobrego ducha i naturalnej wesooci podczas rekreacji , wszyscy go lubili. Rankiem dnia 10 dnia grudnia 1846 roku zoy profesj . Nastpnie rozpocz kurs filozofii i teologii. ycie studenta byo fizycznie i intelektualnie cikie, monotonne w swych obowizkach: dzie po dniu , rok po roku. Spdza godziny przed Najwitszym Sakramentem: od 2 do 3 rano, potem od 6 do 9 rano.  Rne obowizki byy poprzedzielane nauk i modlitw. Musia domyla si, e s plany wysania go do Anglii i by przygotowywany  nie tylko do szerzenia wiary katolickiej , ale i do znoszenia rnych atakw. O. Dominik , doskonay teolog przygotowywa w tym kierunku modych pasjonistw.\n                Dnia 21 grudnia 1850 r. o. Karol zosta wywiecony przez biskupa Labisa, biskupa Tournai.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Bagam Ci, przylij mi czowieka, dobrego zakonnika, zamiast uczonego Salomona , pisa o. Dominik do swego przeoonego w Rzymie. Takim by Karol Houben. Sze miesicy po wiceniach Karol przyby do Staffordshire w Anglii.\n                Od tego czasu Karol Houben zacz gorliwie pracowa dla zbawienia ludzi w Irlandii i Anglii. Spotkao go wiele przykroci i przeszkd. Za jego przyczyn dokonyway si take uzdrowienia, co wywoao ze strony wadz angielskich pewne zaniepokojenie. Ale przede wszystkim zasyn jako kierownik duchowy, spowiednik i przewodnik biedoty irlandzkiej w Anglii i samej Irlandii. By duszpasterzem ludzi najuboszych, zwaszcza robotnikw irlandzkich w Anglii. Chorowa dugo , ale nie stroni od umartwie. Take przeywa stany mistyczne. Zmar 5  stycznia 1893. Jan Pawe II beatyfikowa go w roku 1988. Kanonizowa go papie Benedykt XVI w 2007 r. w Rzymie.\n            ")])])])])}]
